import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Employeur from '../components/letter/Employeur.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedEmployeur = connect(
  mapStateToProps,
  mapDispatchToProps
)(Employeur)

export default ConnectedEmployeur


