import React, {Component} from 'react'
import classNames from 'classnames'
import style from '../../styles/DataTable.scss'
import button from '../../styles/buttons.scss'
import moment from 'moment'
import _ from 'lodash'

import CustomInput from '../utils/CustomInput'

export const DataTableGroup = (props) => {
    return (
        <div>
            <div className={style.row}>
                <h3>{props.title || ' '}</h3>
                <div>
                    {props.onAdd}
                    {props.onRemove}
                </div>
            </div>
            <div className={style.group}>
                {props.children}
            </div>
        </div>
    )
}

export const DataTableItem = props => {
    const renderValue = () => {
        if(props.updatable) return

        if(_.isObject(props.value)) {
          if(props.type == 'address'){
            return (<div key='adress'>
                        <div className="col-md-12">
                          <h4>Voie</h4> <div className={style.vitem}>{props.value['way']}</div>
                        </div>
                        <div className="col-md-6">
                          <h4>Code Postal</h4> <div className={style.vitem}>{props.value['code']}</div>
                        </div>
                        <div className="col-md-6">
                          <h4>Ville</h4> <div className={style.vitem}>{props.value['city']}</div>
                        </div>
                    </div>)
          }else if(props.type == 'birthdate'){
            return (<div key='birthdate' style={{paddingRight: 15+"px", paddingLeft: 15+"px" }}>
                <div className={style.vitem}>{props.value['day']}/{props.value['month']}/{props.value['year']}</div>
            </div>)
          }else if(props.type == 'interlocuteur'){

            let civility = props.value['civility']

            switch (props.value['civility']) {
              case "0":
                civility = "Madame"
                break
              case "1":
                civility = "Monsieur"
                break
              case "2":
                civility = ""
                break
            }

            return (<div key='interlocuteur'>
              <div className="col-md-2">
                <h4>Civilité</h4> <div className={style.vitem}>{civility}</div>
              </div>
              <div className="col-md-5">
                <h4>Nom</h4> <div className={style.vitem}>{props.value['lastname']}</div>
              </div>
              <div className="col-md-5">
                <h4>Prénom</h4> <div className={style.vitem}>{props.value['firstname']}</div>
              </div>
            </div>)
          }else{
            return _.map(props.value, (item, key) => (
                <div className={style.vitem}>{item}</div>
            ))
          }
        } else {
            let value = props.value
            switch (props.type) {
                case 'boolean':
                    value = value === 1 ? 'Oui' : 'Non'
                    break
            }
            return (
              <div className="col-md-12">
                <div className={style.vitem}>{value}</div>
              </div>)

        }
    }

    return (
        <div className={style.item}>
            <div className={style.title}>
                <h4>{props.title}</h4>
            </div>
            <div className={style.value}>
                {props.updatable && (<CustomInput value={props.value} values={props.values} placeholder={props.title} type={props.type} onChange={props.onChange}/>)}
                {renderValue()}
            </div>
        </div>
    )
}

export const DataTable = (props) => {
    return (
        <form className={style.main} onSubmit={() => props.onSubmit && props.onSubmit()}>
            {props.children}
        </form>
    )
}

DataTable.Item = DataTableItem
DataTable.Group = DataTableGroup

export default DataTable



