import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Don from '../components/elements/Don.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedDon = connect(
  mapStateToProps,
  mapDispatchToProps
)(Don)

export default ConnectedDon


