import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Notaire from '../components/letter/Notaire.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedNotaire = connect(
  mapStateToProps,
  mapDispatchToProps
)(Notaire)

export default ConnectedNotaire


