import React from "react";
import _ from 'lodash'
import classNames from 'classnames'
import PortalWithState from 'react-portal'
import { Link } from 'react-router-dom'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from '../elements/Page'

import { getFileTypes, getTypesByGroup } from '../../scripts/fileTypes'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleRetraite from '../../styles/Retraite.scss'
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'
import { DOCTYPE_CONTACTS, DOCTYPE_F_CONTACTS, DOCTYPE_F_CUSTOMCONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_META } from "../../constants/index";
import { getFields, getInfosFromDoctype } from "../../scripts/dataFields";
import DataForm from "../../containers/DataForm";
import { contactExist, getMeta, getCustomContactMeta } from "../../scripts/meta";
import { string_to_slug } from "../../tools/index";
import { getContactMeta } from "../../scripts/onboarding";
import customtypes from '../../constants/types.json'
import HoverImage from "react-hover-image"
import html2canvas from 'html2canvas';
import * as jsPDF  from 'jspdf'

export default class Organisme extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            caisse: '............................',
            numeroAdresse: '............................',
            rue: '',
            postale: '............................',
            ville: '',
            pays: '',
            telephone: '',
            telecopie: '',
            email: '',
            immatriculation: '............',
            compte: '............',
            faite: '..........................',
            recu: '',
            faitePar: '',
            error: ''
        };
        this.handleLetter = this.handleLetter.bind(this)
    }


    printElem() {
        const input = document.getElementById('letter');
        html2canvas(input)
          .then((canvas) => {
            const dataURL  = canvas.toDataURL('image/jpeg');
            const downloadLink = document.createElement('a');
            downloadLink.href = dataURL;
            downloadLink.download = 'lettre-organisme.png';
            downloadLink.style.display = 'nones';
            document.body.appendChild(downloadLink);
            downloadLink.click();
            // console.log(imgData);
            // window.open(imgData);
            // const pdf = new pdfConverter('p', 'mm', 'a4');
            // pdf.fromHTML(window.document.getElementById('letter'), 10, 10,{'width': 180});
            // // pdf.addImage(imgData, 'JPEG', 0, 0);
            // // pdf.output('dataurlnewwindow');
            // pdf.save("download.pdf");
          })
        ;
    }

    handleLetter() {
        if (document.getElementById('caisse').value == '' || document.getElementById('numeroAddresse').value == '' || document.getElementById('rue').value == ''
            || document.getElementById('postale').value == '' || document.getElementById('ville').value == '' || document.getElementById('pays').value == ''
            || document.getElementById('telephone').value == '' || document.getElementById('telecopie').value == '' || document.getElementById('email').value == ''
            || document.getElementById('immatriculation').value == ''|| document.getElementById('compte').value == '' || document.getElementById('faite').value == '' || document.getElementById('recu').value == ''
            || document.getElementById('faitePar').value == '') {
            this.setState({
                error: "Veuillez compléter l'intégralité des informations"
            })
        } else {
            this.setState({
                caisse: document.getElementById('caisse').value,
                numeroAdresse: document.getElementById('numeroAddresse').value,
                rue: document.getElementById('rue').value,
                postale: document.getElementById('postale').value,
                ville: document.getElementById('ville').value,
                pays: document.getElementById('pays').value,
                telephone: document.getElementById('telephone').value,
                telecopie: document.getElementById('telecopie').value,
                email: document.getElementById('email').value,
                immatriculation: document.getElementById('immatriculation').value,
                compte: document.getElementById('compte').value,
                faite: document.getElementById('faite').value,
                recu: document.getElementById('recu').value,
                faitePar: document.getElementById('faitePar').value,
                error: ''
            })
        }
    }

    render() {
        return (
            <Page title="Sahra Vadrot" subtitle="Cet espace vous permet de renseigner l'ensemble des informations qui seront utiles pour prévenir les caisses de retraite ">
                <div className={style.main}>
                    <div >
                        <div>
                            <h3 className={styleRetraite.titleRetraite} >Les organismes payeurs</h3>
                        </div>
                        <p className={styleRetraite.dataTxt} >
                            <b>La préfecture ou sous-préfecture</b> pour modifier la carte grise d’un véhicule.
                            Lorsqu’un véhicule est immatriculé au nom du défunt, les proches peuvent l’utiliser le temps de
                            régler la succession (sous réserve de l’accord de l’assureur). Par la suite, celui qui conserve
                            le véhicule doit faire établir une carte grise à son nom en retirant un formulaire auprès de la
                            préfecture, fournir une pièce d’identité, un justificatif de domicile, une attestation du notaire ou un
                            acte de notoriété ainsi qu’une lettre de désistement des autres héritiers ou un certificat du notaire
                            signifiant leur accord. Il n’existe aucun délai obligatoire pour effectuer la démarche de modification
                            de la carte grise lorsque le conducteur du véhicule est le conjoint survivant. Dans les autres cas, la
                            carte grise doit être modifiée en préfecture dans les quinze jours.
                            Le contrat d’assurance du véhicule est maintenu après le décès. Il appartient ensuite aux héritiers,
                            selon les cas, de transférer ou de résilier le contrat. <br />
                            Si les héritiers vendent le véhicule moins de trois mois après le décès, ou si celui-ci n’a pas circulé
                            depuis le décès, l’établissement d’une nouvelle carte grise n’est pas nécessaire pour la vente.
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            <b>Le conseil général</b> et autres organismes pour faire cesser les différentes allocations (Apa :
                            allocation personnalisée d’autonomie, autres prestations sociales...).
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            <b>La Poste</b> pour le réacheminement du courrier.
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            <b>Électricité - Eau - Abonnements téléphoniques - Abonnements journaux, revues...</b> pour
                            résiliation des contrats ou désignation d’un nouveau titulaire
                        </p>
                    </div>
                    <div>
                        <h3 className={styleRetraite.titleRetraite} >Coordonnées</h3>

                        <div className={styleRetraite.contentForm} >
                            <div>
                                <label>Orgasnime</label>
                                <input type="text" id="caisse" placeholder="Nom de l'orgasnime" className={styleRetraite.inputForm} />
                            </div>

                            <div>
                                <label>adresse</label>
                                <div className={styleRetraite.adresse}>
                                    <input type="text" id="numeroAddresse" placeholder="191" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="rue" placeholder="Rue Jean Jaures" className={[styleRetraite.inputForm, styleRetraite.sizeRue].join(' ')} />
                                </div>
                            </div>
                            <div>
                                <div className={styleRetraite.adresse}>
                                    <input type="text" id="postale" placeholder="75002" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="ville" placeholder="Paris" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="pays" placeholder="France" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                </div>
                            </div>
                            <div>
                                <div className={styleRetraite.labelAdresse}>
                                    <label>numéro de téléphone</label>
                                    <label>numéro de télécopie</label>
                                    <label>email</label>
                                </div>
                                <div className={styleRetraite.adresse}>
                                    <input type="text" id="telephone" placeholder="01 84 16 32 56" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="telecopie" placeholder="01 84 16 32 57" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="email" placeholder="contact@organisme.fr" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                </div>
                            </div>
                            <h3 className={styleRetraite.titleRetraite} >Information sur le contrat</h3>
                            <div>
                                <label>n° de contrat</label>
                                <input type="text" id="immatriculation" placeholder="11713792" className={styleRetraite.inputForm} />
                            </div>
                            <div>
                                <label>n° de compte bancaire</label>
                                <input type="text" id="compte" placeholder="11713792" className={styleRetraite.inputForm} />
                            </div>
                            <h3 className={styleRetraite.titleRetraite} >Suivi de dossier</h3>
                            <div className={styleRetraite.labelDemarche}>
                                <label>Démarche faite le </label>
                                <label>Acte reçu le </label>
                                <label>Démarche faite par</label>
                            </div>
                            <div className={styleRetraite.adresse}>
                                <input type="text" id="faite" placeholder="12/12/2017" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                <input type="text" id="recu" placeholder="13/12/2017" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                <input type="text" id="faitePar" placeholder="Mr Dupont Pierre" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                            </div>
                            <div>
                                <label>suite donnée ou à donner</label>
                                <input type="text" placeholder="à imprimer" className={styleRetraite.inputForm} />
                            </div>
                            <div className={styleRetraite.divButton} >
                                <h3 className={styleRetraite.titleRetraite} >Courrier type</h3>
                                <button className={styleRetraite.buttonCustom} onClick={this.handleLetter}  >
                                    METTRE A JOUR
                                </button>
                                <button className={styleRetraite.buttonCustom} onClick={this.printElem}  >
                                    IMPRIMER
                                </button>
                            </div>
                            <h3> {this.state.error} </h3>
                        </div>

                    </div>
                    <page className={styleRetraite.afour} id='letter'  >
                        <div className={styleRetraite.expediteur}  >
                            <p>Expéditeur</p>
                            <p><span>{this.state.faitePar}</span></p>
                            <p><span>............................</span></p>
                        </div>
                        <div className={styleRetraite.ToCaisse}  >
                            <p>Aux organismes payeurs</p>
                            <p><span> {this.state.caisse} </span></p>
                            <p><span> {this.state.numeroAdresse} {this.state.rue}</span></p>
                            <p><span> {this.state.postale} {this.state.ville} {this.state.pays} </span></p>
                            <p className={styleRetraite.date}  >Le <span>{this.state.faite}</span></p>
                        </div>
                        <div className={styleRetraite.contentLetter}  >
                            <p>Madame, Monsieur,</p>
                            <p>
                                Suite au décès de Monsieur (Madame ou Mademoiselle), <span>.............</span>, mon/ma (indiquer le lien de parenté)
                                <span>...........</span>, survenu le <span>.............</span>, domicilié .............................................<br/>
                                ........................................................................................................................<br/>
                                je procède aux formalités qui m'incombent.
                            </p>
                            <p>
                                A ces fins, e vous saurai gré de : <br />
                                - de résilier l'abonnement ou contrat n° {this.state.immatriculation} <br />
                                - de transférer cet abonnement ou contrat au nom de ......................... <br/>
                                - d'effectuer le prélèvement sur le n° de compte {this.state.compte} <br/>
                                (RIB/RIP ci-joint).

                            </p>
                            <p>
                                Je vous prie d'agréer, Madame, Monsieur, l'expression de mes sentiments les meilleurs.
                            </p>
                            <p className={styleRetraite.signature}  >Signature</p>
                        </div>
                        <div className={styleRetraite.mentionBanque} >
                            <p>P.J. : acte de décés</p>
                            <p>Courrier en recommandé avec accusé de réception</p>
                        </div>
                    </page>
                </div>
            </Page>
        );
    }
}



