import React, {Component} from 'react'
import { withRouter, Link } from 'react-router-dom'
import style from '../../styles/Navigation.scss'

export const Navigation = (props) => {
    return (
        <nav className={style.main}>
           <div className={style.nav}>
           <div className={style.divLogo}  >
                <img className={style.logoMaif}  src="public/media/maiflogo.svg" />
                <h4> Faire face au décès </h4>
           </div>
                <ul className={style.list}>
                    <li className={style.button}>
                        <ActiveLink to="/home" activeClassName={style.active} title='Accueil'>
                            <img  className={style.chevron} src="public/media/chevronpurple.svg" /> Accueil
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/funerals" activeClassName={style.active} title='Préparer mes Obsèques'>
                        <img  className={style.chevron} src="public/media/chevronpurple.svg" /> Préparer mes Obsèques
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/relation" activeClassName={style.active} title='Mes Proches'>
                            <img  className={style.chevron} src="public/media/chevronpurple.svg" /> Mes Proches
                        </ActiveLink>
                    </li>
                      <li className={style.button}>
                        <ActiveLink to="/assistances" activeClassName={style.active} title='Services MAIF'>
                            <img  className={style.chevron} src="public/media/chevronpurple.svg" /> Services MAIF
                        </ActiveLink>
                    </li>

                </ul>
             <hr className={style.seperator}/>
                <ul className={style.list}>
                        <li className={style.help} >
                            <p>AIDES</p>
                        </li>
                        <li className={style.button}>
                            <ActiveLink to="/contact" activeClassName={style.active} title='Contact'>
                                <img  className={style.chevron} src="public/media/chevronpurple.svg" /> Contact
                            </ActiveLink>
                        </li>
                        <li className={style.button}>
                            <ActiveLink to="/faq" activeClassName={style.active} title='FAQ'>
                                <img  className={style.chevron} src="public/media/chevronpurple.svg" /> FAQ
                            </ActiveLink>
                        </li>
                </ul>
           </div>
          <hr className={style.seperator}/>
        </nav>
    );
}

export const CustomLink = (props) => {
    const open = (e) => {
        e.preventDefault()
        if(props.onClick) props.onClick()
        props.history.push(props.to)
    }

    const { router, location, to, activeClassName, children, title } = props

    const active = location.pathname.indexOf(to) === 0

    return (
        <a className={active && activeClassName ? activeClassName : undefined} onClick={e => open(e)} title={title}>
            {children}
        </a>
    )
}

const ActiveLink = withRouter(CustomLink)

export default Navigation



