import React, {Component} from 'react'
import classNames from 'classnames'
import style from '../../styles/Contacts.scss'

import {VIEW_GRID, VIEW_LIST} from '../../constants'

export const ContactList = (props) => {

    return (
        <div className={classNames(style.list, {
            [style.viewList]: props.view === VIEW_LIST,
            [style.viewGrid]: props.view === VIEW_GRID
        })}>
            {props.children.map((item, key) => {
                let title = undefined
                if(item.lastName && item.firstName) {
                    title = item.firstName + " " + item.lastName
                } else if(item.lastName) {
                    title = item.lastName
                } else if(item.firstName) {
                    title = item.firstName
                }

                return (
                    <div className={style.divContact} >
                    <div className={style.contentContact}  >
                        <p className={style.contactName} > {title} </p>
                        <img  className={style.chevron} src="public/media/chevronpurple.svg" />
                    </div>
                    <hr className={style.seperator}/>
                  </div>

                )
            })}
        </div>
    )
}

export default ContactList



