import React, {Component} from 'react'
import DatePicker from 'react-datepicker'
import moment from 'moment'
import '../../styles/react-datepicker.css';

export default class CustomInput extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            value: ''
        }
    }

    componentWillMount() {
        this.state.value = this.props.value
    }

    componentWillReceiveProps (newProps) {
        this.state.value = newProps.value
    }

    onChangePhone(value) {
        // check if is a phone number
        let checkPhone = /0\d?([. -]?\d\d){0,4}\d?/

        this.onChange(value)
    }

    onChange(value) {
        if(this.props.type === "date") {
            if(value && value.isValid()) {
                value = value.format('DD/MM/YYYY')
            } else {
                value = moment().format('DD/MM/YYYY')
            }
        }

        this.setState({value})
        this.props.onChange(value)
    }

    onChangeMulti(v, index) {
        let value = this.state.value
        if(typeof value !== 'object') value = {}

        value[index] = v

        this.setState({value})
        this.props.onChange(value)
    }

    render() {
        var value = this.state.value
        var type = this.props.type || 'text'

        switch (type) {
            case 'boolean':
                return (
                    <select autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} onChange={(e) => this.onChange(e.target.value) }>
                        <option value={0}>Non</option>
                        <option value={1}>Oui</option>
                    </select>
                )

            case 'list':
                if((!value || value === '') && this.props.values) {
                    setTimeout(() => this.props.onChange(this.props.values[0]))
                }
                return (
                    <select autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} onChange={(e) => this.onChange(e.target.value) }>
                      {this.props.values.map((item, key) => (
                        <option key={key} value={item}>{item}</option>
                      ))}
                    </select>
                )

            case 'listYears':
                const currentYear = new Date().getFullYear()
                const minYear = 1900

                const years = []
                for(let i = minYear; i <= currentYear; i++) years.unshift(i)

                if((!value || value === '')) {
                    setTimeout(() => this.props.onChange(currentYear))
                }
                return (
                    <select autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} onChange={(e) => this.onChange(e.target.value) }>
                        {years.map((item, key) => (
                            <option key={key} value={item}>{item}</option>
                        ))}
                    </select>
                )

            case 'date':

                if(!value){
                    value = moment()
                    // this.props.onChange(value.format('DD/MM/YYYY'))
                } else {
                    value = moment(value, 'DD/MM/YYYY')
                }

                return (
                      <DatePicker
                      dateFormat="DD/MM/YYYY"
                      locale="fr-fr"
                      isClearable={false}
                      selected={value}

                      dropdownMode="select"
                      onChange={e => this.onChange(e)} />
                )

            case 'email':
                return (
                    <input type="email" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} onChange={(e) => this.onChange(e.target.value)} placeholder={this.props.title}/>
                )

            case 'telephone':
                return (
                    <input type="tel" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} onChange={(e) => this.onChangePhone(e.target.value)} placeholder={this.props.title}/>
                )

            case 'textarea':
                return (
                    <textarea autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} onChange={(e) => this.onChange(e.target.value) } placeholder={this.props.title}></textarea>
                )

            case 'contract':
                value = value || {}
                return (
                    <div>
                        <input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.company} onChange={e => this.onChangeMulti(e.target.value, "company")} placeholder={"Société"}/>
                        <input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.address} onChange={e => this.onChangeMulti(e.target.value, "address")} placeholder={"Adresse"}/>
                        <input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.email} onChange={e => this.onChangeMulti(e.target.value, "email")} placeholder={"Email"}/>
                        <input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.telephone} onChange={e => this.onChangeMulti(e.target.value, "telephone")} placeholder={"Téléphone"}/>
                    </div>
                )

            case 'address':
                value = value || {}
                return (
                    <div className="adress">
                      <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 100 +'%'}}><input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.way} onChange={e => this.onChangeMulti(e.target.value, "way")} placeholder={"Voie"}/></div>
                      <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 50 +'%', marginTop: 10+ 'px'}}  ><input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.code} onChange={e => this.onChangeMulti(e.target.value, "code")} placeholder={"Code postal"}/></div>
                      <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 50 +'%', marginTop: 10+ 'px'}}  ><input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.city} onChange={e => this.onChangeMulti(e.target.value, "city")} placeholder={"Ville"}/></div>
                    </div>
                )

            case 'birthdate':
                value = value || {}
                return (
                    <div>
                        <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 33 +'%'}} ><input type="number" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.day} onChange={e => this.onChangeMulti(e.target.value, "day")} placeholder={"Jour"}/></div>
                        <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 33 +'%'}} ><input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.month} onChange={e => this.onChangeMulti(e.target.value, "month")} placeholder={"Mois"}/></div>
                        <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 33 +'%'}} ><input type="number" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.year} onChange={e => this.onChangeMulti(e.target.value, "year")} placeholder={"Année"}/></div>
                    </div>
                )

            case 'interlocuteur':
                value = value || {}
                return (
                    <div>
                        <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 33 +'%'}} >
                          <select autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.civility} onChange={e => this.onChangeMulti(e.target.value, "civility") }>
                              <option key="Madame" value={0}>Madame</option>
                              <option key="Monsieur" value={1}>Monsieur</option>
                              <option key="Autre" value={2}>Autre</option>
                          </select>
                        </div>
                        <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 33 +'%'}} ><input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.lastname} onChange={e => this.onChangeMulti(e.target.value, "lastname")} placeholder={"Nom de famille"}/></div>
                        <div style={{paddingRight: 1+"%", boxSizing: "border-box", display: "inline-block", width: 33 +'%'}} ><input type="text" autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value.firstname} onChange={e => this.onChangeMulti(e.target.value, "firstname")} placeholder={"Prénom"}/></div>
                    </div>
                )

            default:
                return (
                  <input type={type} autoFocus={this.props.autoFocus} className={this.props.className} style={this.props.style} value={value} placeholder={this.props.title} onChange={e => this.onChange(e.target.value) }/>
                )

        }
    }
}




