import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import button from '../../styles/buttons.scss'
import style from '../../styles/Contacts.scss'
import styleDossier from '../../styles/Dossier.scss'
import ToolBoxOptions from './ToolBoxOptions'
import _ from 'lodash'
import { Link } from 'react-router-dom'
import PortalWithState from 'react-portal'

import Modal from '../modals/Modal'
import Accordion from 'react-responsive-accordion';
import Collapsible from 'react-collapsible';

import Page from './Page'
import Loader from './Loader'
import ContactList from './ContactList'

import ContactsModal from '../modals/ContactsModal'
import DataForm from "../../containers/DataForm";
import {VIEW_LIST, STATUS_LOAD, STATUS_DONE, DOCTYPE_F_CUSTOMCONTACTS, DOCTYPE_HOMEDATA} from '../../constants'
import {getMeta} from "../../../scripts/meta";
import {DOCTYPE_F_CONTACTS} from "../../../constants";
import {getDataElements} from "../../../tools";
import {getCustomContactMeta} from "../../scripts/meta";
import stylenav from '../../styles/Navigation.scss'
import data from '../../constants/data/shareHome.json';
import {getInfosFromDoctype} from "../../../scripts/dataFields";

const Row = props => {
  return (
    <div onClick={e => props.onAdd && props.onAdd(e)}>
      <div className={classNames(style.row)}>
        <div className={style.name}>
          <div>{props.title} <img  className={stylenav.chevron} src="media/chevronpurple.svg" /></div>
          <div className={style.excerpt}>{props.excerpt}</div>
        </div>
        <div className={classNames(style.check, style.checked, {[style.checked]: props.check})}>
          {props.check && (<img className={style.done} src="public/media/done.png" />)}
          {!props.check && (<img className={style.done} src="public/media/waiting.png" />)}
        </div>
      </div>
      <div className={style.separator}></div>
    </div>

  )
}

export default class Dossier extends Component{

  constructor(props, context) {
    super(props, context)

    this.state = {
      current: null,
      modalOpen: false,
      edition: false,
      id: 0,
      data: [],
      customData: [],
      getdata:  false
    };


    getMeta()
      .then(result => {
        this.setTypeGroup(result[0].value)

        getDataElements(DOCTYPE_F_CONTACTS)
          .then(res => {
            if(res.length > 0) {
              let type = result[0].value;
              for(let n in type[0].types){
                let exist = _.find(res, t => t.type === type[0].types[n].name)
                if(exist){
                  type[0].types[n].checked = true;
                  type[0].types[n]._id = exist._id;
                }
              }
              this.setTypeGroup(type)
            }

            console.log(res)
            this.state.data = res;
            console.log(this.state.data);
            this.setState({getData: true});
            console.log( this.state.getData);
          })

      });

    getCustomContactMeta()
      .then(result => {
        this.setCustomTypeGroup(result[0].value)

        getDataElements(DOCTYPE_F_CUSTOMCONTACTS)
          .then(res => {
            if(res.length > 0) {
              let type = result[0].value;
              for(let n in type[0].types){
                let exist = _.find(res, t => t.type === type[0].types[n].name)
                if(exist){
                  type[0].types[n].checked = true;
                  type[0].types[n]._id = exist._id;
                }
              }
              this.setCustomTypeGroup(type)
            }
            console.log(res)
            this.state.customData = res;
            console.log(this.state.customData);
          })

      });

    this.getID = this.getID.bind(this);

    cozy.client.settings.getInstance().then(res => {
      this.user = res.attributes;
      console.log(this.user);
    })
  }

  setTypeGroup(typeGroup){
    this.setState({typeGroup: typeGroup})
    this.render()
  }

  setCustomTypeGroup(typeGroup){
    this.setState({customTypeGroup: typeGroup})
    this.render()
  }

  clickOnContacts(data = '') {
    console.log(data)
    const item = getInfosFromDoctype(DOCTYPE_F_CONTACTS);
    this.setState({current:item.slug, formType: data.key, open: true, hiddenType: data.name});
    console.log(this.state);
  }

  getID(name) {
    console.log(name);

    let i = 0;

    while (i < this.state.data.length)  {
      if (this.state.data[i].type == name) {
        return this.state.data[i]._id;
      }
      i++;
    }

    return null
  }

  getValue(name) {
    let i = 0;

    while (i < this.state.data.length)  {
      if (this.state.data[i].type == name) {
        return true
      }
      i++;
    }

    return false
  }

  render() {
    return (
      this.state.getData == true ?
      <Page title={this.user.public_name} subtitle="Ce dossier s’adresse aux proches référents. Il contient les dernières volontés du titulaire et un assistant administratif qui hiérarchise et automatise les démarches.">
        <Collapsible triggerOpenedClassName={style.titlePageCollapse} triggerClassName={style.titlePageCollapse} trigger="Informations personnelles du défunt">
        <div className={style.data}  >
          <Link to="/profil">
            <div className={style.divContact} >
              <div className={style.contentContact}  >
                <p className={style.contactName} >Profil de {this.user.public_name}</p>
                <img  className={style.chevron} src="public/media/chevronpurple.svg" />
              </div>
              <hr className={style.seperator}/>
            </div>
          </Link>
          <Link to="/dernieres-volontees">
            <div className={style.divContact} >
              <div className={style.contentContact}  >
                <p className={style.contactName} >Ses dernières volontés</p>
                <img  className={style.chevron} src="public/media/chevronpurple.svg" />
              </div>
              <p>Expliquez à vos proches vos attentes pour ses obsèques</p>
              <hr className={style.seperator}/>
            </div>
          </Link>
          <Link to="/relation">
            <div className={style.divContact} >
              <div className={style.contentContact}  >
                <p className={style.contactName} >Les proches référents</p>
                <img  className={style.chevron} src="public/media/chevronpurple.svg" />
              </div>
              <p>Le titulaire du compte désigne un ou plusieurs de ses proches qui auront accès aux données après le décès. Ils les utiliseront pour organiser les obsèques selon les choix et pourront s'acquitter aisément de l'ensemble des démarches administratives. Le titulaire désigne cette ou ces personne(s) de confiance ici</p>
              <hr className={style.seperator}/>
            </div>
          </Link>
          <Link to="/relation">
            <div className={style.divContact} >
              <div className={style.contentContact}  >
                <p className={style.contactName} >Proches à prévenir</p>
                <img  className={style.chevron} src="public/media/chevronpurple.svg" />
              </div>
              <p>Le titulaire entre ici la liste des personnes qui seront prévenues de son décès. Notamment celles dont les coordonnées ne sont peut-être pas connues des proches référents (voisin, ami d’enfance, femme de ménage …).</p>
              <hr className={style.seperator}/>
            </div>
          </Link>

          <Link to="/presta-funeraire">
            <div className={style.divContact} >
              <div className={style.contentContact}  >
                <p className={style.contactName} >Prestataire funéraire</p>
                <img  className={style.chevron} src="public/media/chevronpurple.svg" />
              </div>
              <p> Ce service vous permet de géolocaliser les prestataires funéraires, faire des demandes de devis, stocker le devis retenu.</p>
              <hr className={style.seperator}/>
            </div>
          </Link>
          <Link to="/don-corps">
            <div className={style.divContact} >
              <div className={style.contentContact}  >
                <p className={style.contactName} >Don du corps à la science</p>
                <img  className={style.chevron} src="public/media/chevronpurple.svg" />
              </div>
              <p>Toutes les informations pour donner son corps à la science ou pour stipuler son refus de don d’organe.</p>
              <hr className={style.seperator}/>
            </div>
          </Link>
        </div>
        </Collapsible>
          <Collapsible triggerOpenedClassName={style.titlePageCollapse} triggerClassName={style.titlePageCollapse} trigger="Démarches Administratives à réaliser">

        <div className={style.row} >
          <p> Un décès occasionne nombre de formalités. Notre assistant administratif hiérarchise
            les priorités, synchronise les données personnelles (noms, adresses, contacts ...) avec les courriers-types et permet le suivi de chaque démarche.
          </p>
        </div>
        <div className={style.dataObseque}  >
          {this.state.typeGroup.map((group, key) => {
            let docs = group.types
            if(docs && docs.length > 0) {
              console.log(docs);
              return (

                <li key={key}>
                  <ul className={style.list}>
                    {docs.map((item, key) => (
                        <li  key={key}>
                          <Row id={item.slug} title={item.name} excerpt={item.excerpt}  check={item.checked} unique={true}
                               onAdd={e => {
                                 if( item.checked) {
                                   this.props.history.push("/letter/" + item.key + "/" + item._id)
                                 } else {
                                   this.clickOnContacts(item);
                                 }
                               }}/>
                        </li>
                    ))}
                  </ul>
                </li>
              )
            } else {
              return null
            }
          })}

          {this.state.customData.map((item) => {
            console.log(item);
            return (
              <span>
                  <div className={styleDossier.delai}  >
                    <p>DANS LES 6 MOIS</p>
                  </div>
                  <Link to={"/letter/" + item.formType + "/" + item._id}>
                    <div className={style.divContact} >
                      <div className={style.contentContact}  >
                      <p className={style.contactName} >{item.type}</p>
                        <img  className={style.chevron} src="public/media/chevronpurple.svg" />
                        <img className={style.done} src="public/media/done.png" />
                      </div>
                      <p>LOREM IPSU</p>
                      <hr className={style.seperator}/>
                    </div>
                  </Link>
                </span>
            )})}
        </div>
          </Collapsible>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.open} closeOnEsc onClose={() => this.setState({open: false})}>
          <Modal>
            <DataForm
              closeModal={this.props.history.replace}
              slug={this.state.current}
              hiddenType={this.state.hiddenType}
              formType={this.state.formType}
              onboard={false}
              close={()=>{this.setState({open: false})}}
            />
          </Modal>
        </PortalWithState>
      </Page>

        : <div> Loading ....</div>
  )
  }
}



