import { connect } from 'react-redux'
import { importerClick } from '../actions'
import CaisseAllocation from '../components/letter/CaisseAllocation.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedCaisseAllocation = connect(
  mapStateToProps,
  mapDispatchToProps
)(CaisseAllocation)

export default ConnectedCaisseAllocation


