import { connect } from 'react-redux'
import { dataNew } from '../actions'
import DataForm from '../components/elements/DataForm'
import {dataSave} from "../actions/data";

const mapDispatchToProps = dispatch => {
    return {
        onNewData: (doctype, data) => {
            return dispatch(dataNew(doctype, data))
        },
        onSaveData: (doctype, id, data) => {
            return dispatch(dataSave(doctype, id, data))
        }
    }
}

const ConnectedDataForm = connect(
    null,
    mapDispatchToProps
)(DataForm)

export default ConnectedDataForm



