import React from "react";
import _ from 'lodash'
import classNames from 'classnames'
import PortalWithState from 'react-portal'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from './Page'

import {getFileTypes, getTypesByGroup} from '../../scripts/fileTypes'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import stylenav from '../../styles/Navigation.scss'
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'
import {DOCTYPE_CONTACTS, DOCTYPE_F_CONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_F_YOUNES} from "../../constants/index";
import {getFields, getInfosFromDoctype} from "../../scripts/dataFields";
import DataForm from "../../containers/DataForm";
import { markAsShown, isAlreadyShown } from '../../scripts/onboarding'



const Doctypes = [
  {
    doctype: DOCTYPE_F_LASTWILL,
    display: 'Mes dernieres volontés',
    excerpt: 'Expliquez à vos proches vos attentes pour vos obsèques',
    path: '/funerals/lastwill',
    slug: 'lastwill',
    internpath: false,
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'Don du corps à la science',
    excerpt: "Toutes les informations pour donner son corps à la science ou pour stipuler son refus de don d’organe.",
    path: '/don',
    internpath: '/don',
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'Refus du don d\'organe ',
    excerpt: "Toutes les informations pour donner son corps à la science ou pour stipuler son refus de don d’organe.",
    path: '/don',
    internpath: '/don',
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'Mes Proches Référents',
    excerpt: "Il s'agit des personnes qui auront accès à vos informations après votre décès.",
    path: '/funerals/works',
    internpath: '/relation',
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'L\'employeur ou Pôle Emploi ' ,
    excerpt: "Lorem ipsum dolor sit amet",
    path: '/funerals/works',
    internpath: '/relation',
    unique: true
  }
]

const Row = props => {

  const button = props.unique ? (props.check ? "Modifier" : "Compléter") : (props.check ? "Ajouter un nouveau" : "Compléter")

  return (
    <div onClick={e => props.onAdd && props.onAdd(e)}>
      <div className={classNames(style.row)}>
        <div className={style.name}>
          <div>{props.title} <img  className={stylenav.chevron} src="public/media/chevronpurple.svg" /></div>
          <div className={style.excerpt}>{props.excerpt}</div>
        </div>
        {/* <div className={classNames(style.check, style.checked, {[style.checked]: props.check})}>
          {props.check && (<i className="ion-checkmark-circled"></i>)}
          {!props.check && (<div className={style.circle}>0%</div>)}
        </div> */}
        {/* <div  className={classNames(style.button)}><i className="ion-ios-arrow-forward"></i>{button}</div> */}
      </div>
      <div className={style.separator}></div>
    </div>

  )
}
// FuneralList page component
export  default class Home extends React.Component {
  constructor(props) {
    super(props)

    this.fileTypes = getFileTypes()
    this.typeGroup = getTypesByGroup()

    this.state = {
      open: false,
      openOnboarding: false,
      openFirstOnboarding: false,
      current: '',
      InfoOpen: false,
      infoHeader: "",
      infoText: "",
      hiddenType: '',
      search: ''
    }
  }

  componentWillMount(){
    this.computeWidth(this.props)
    this.steps = 1;

    isAlreadyShown()
      .then((res) => {
        if (res) {
          return this.closeFirstOnboarding(true)
        }

        return markAsShown(true)
          .then(() => {
            this.openFirstOnboarding()
          })
      })
  }

  componentWillReceiveProps(newProps) {
    this.computeWidth(newProps)
  }

  computeWidth(props) {
    this.width = 0
    const notPersoFileTypes = this.fileTypes.filter(f => !f.perso)
    const notPersoUsed = props.usedType.filter(elem => _.find(notPersoFileTypes, f => f.key === elem.type) === undefined)
    const totalUsed = notPersoUsed.length + props.dataCreated.length
    const totalTypes = notPersoFileTypes.length + Doctypes.length
    this.width = (totalUsed / totalTypes) * 100
  }

  clickDocument(key, location) {
    this.props.onFileClick(location, key)
    this.setState({open: true})
  }
  clickOnWill(data) {
    const item = getInfosFromDoctype(data.doctype);
    this.setState({current:item.slug, open: true});
  }
  closeOnboarding(){
    this.setState({openOnboarding: false});
    this.forceUpdate()
  }
  openOnboarding(){
    this.setState({openOnboarding: true});
  }
  openFirstOnboarding(){
    this.setState({openFirstOnboarding: true});
  }

  closeFirstOnboarding(init = false){
    if(init){
      this.setState({openFirstOnboarding: false});
    }
    else{
      this.setState({openFirstOnboarding: false, openOnboarding: true});
    }
  }
  clickOnContacts(data = '') {
    const item = getInfosFromDoctype(DOCTYPE_HOMEDATA);
    this.setState({current:item.slug, open: true, hiddenType: data.name});
  }
  clickOninfo(e, header = '', text = '') {
    e.preventDefault()
    e.stopPropagation()
    this.setState({InfoOpen: true, infoText: text, infoHeader: header});
  }
  // render
  render() {
    const add = (<button onClick={() => this.openOnboarding()} className={classNames(button.button, button.default)}><img className={style.add}  src="public/media/add.svg" />RENSEIGNER LES INFORMATIONS</button>)
    return (
      <Page title="Votre espace privé décès" actions={[add]}
            subtitle="Bienvenue dans votre espace privé.
            Quel que soit son âge, on a tous dans l'idée de laisser nos « affaires en ordre »,
            au cas où... Ici, vous avez la possibilité de le faire en toute quiétude, à votre rythme. 
            Commencez, interrompez-vous, reprenez plus tard. L'outil est souple, intuitif, regorge d’informations.
            Visitez-le à votre guise. A mesure que vous avancerez, votre réflexion sur vos dernières volontés pourra s'affiner.
            Et en plus, cet espace est entièrement privé et sécurisé. Il vous est réservé.">
        <div className={style.main}>
          <div className={style.entries}>
            <h3 className={style.titleHome} >Les informations restant à compléter</h3>
            <p className={style.introHome} > L'Espace privé est instructif, riche d'enseignements, pour prendre activement conscience 
                des implications de votre décès pour vos proches. La plateforme s'appréhende comme un outil pratique,
                pour transmettre un relais dans des conditions de clarté et de sérénité. Il vous permet de réfléchir,
                de repérer les éventuels oublis et de simplifier la tâche de ceux qui organiseront vos obsèques selon vos décisions.
              </p>
            <ul className={style.list}>
              {Doctypes.map((item, key) => {
                const checked = _.find(this.props.dataCreated, t => t.doctype === item.doctype)
                if(checked) {
                  return null
                }
                return (
                  <li key={key}>

                    <Row title={item.display} excerpt={item.excerpt}  check={checked} unique={item.unique}
                         onAdd={e => {
                           if(item.unique && checked) {
                             getDataElements(item.doctype)
                               .then(res => {
                                 if(res.length > 0) {
                                   this.props.history.push(`/data/${item.slug}/${res[0]._id}`)
                                 }
                               })
                           } else if(item.internpath){
                             this.props.history.push(item.internpath)
                           }else  if(typeof item.internModal != "undefined" && item.internModal.text != ""){
                             this.clickOninfo(e, item.display, item.internModal.text)
                           }else{
                             this.clickOnWill(item);
                           }
                         }}/>
                  </li>
                )
              })}
            </ul>
            <div className={style.rowAd}>
              <div className={style.firstTitle}>
              </div>
              <div className={style.secondTitle}>
                Portail d'information Espace privé Décès
              </div>
              <div className={style.Adtext}>
                Visitez notre site d'information pour anticiper au mieux vos obsèques
              </div>
              <div className={style.Adlink}>
                <a href="http://maif.mymachinery.fr" target="_blank">EN SAVOIR +</a>
              </div>
            </div>
            <div className={style.separator}></div>
        </div>
        </div>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.open} closeOnEsc onClose={() => this.setState({open: false})}>
          <Modal>
            <DataForm
              closeModal={this.props.history.replace}
              slug={this.state.current}
              hiddenType={this.state.hiddenType}
              onboard={false}
              close={()=>{this.setState({open: false})}}
            />
          </Modal>
        </PortalWithState>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.openOnboarding} closeOnEsc onClose={() => this.setState({openOnboarding: false})}>
          <Modal>
            <DataForm
              closeModal={this.props.history.replace}
              step={this.steps}
              slug={this.state.current}
              onboard={true}
              hiddenType={this.state.hiddenType}
              close={()=>{this.setState({openOnboarding: false})}}
            />
          </Modal>
        </PortalWithState>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.openFirstOnboarding} closeOnEsc onClose={() => this.setState({openFirstOnboarding: false})}>
          <Modal>
            <div className={style.modal} onClick={e => e.stopPropagation()}>
              <img style={{width: 100+'%'}} src="public/media/onboardinginfo.jpg" alt=""/>
              <button onClick={() => this.closeFirstOnboarding()} className={classNames(button.button, button.default)}><i className="ion-ios-plus-empty"></i>SUIVANT</button>
            </div>
          </Modal>
        </PortalWithState>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.InfoOpen} closeOnEsc onClose={() => this.setState({TypeOpen: false})}>
          <Modal>
            <div className={style.ModalMain}>
              <div className={style.edition} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                  <div>
                    <h1>{this.state.infoHeader}</h1>
                  </div>
                </div>
                <form className={style.content}>
                  <div className={style.row}>
                    {this.state.infoText}
                  </div>
                </form>
                <div className={style.footer}>
                  <div className={style.footerRight}>
                    <button
                      onClick={e => this.setState({ InfoOpen: false, infoText: ''})}
                      className={classNames(button.button, button.defaultLight)}
                    >
                      FERMER
                    </button>
                  </div>
                </div>
              </div>

            </div>
          </Modal>
        </PortalWithState>
      </Page>
    )

  }
}


