import React from "react";

import Page from '../elements/Page'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleRetraite from '../../styles/Retraite.scss'

import html2canvas from 'html2canvas';
import data from '../../constants/data/share.json';
import {getDataElements} from "../../../tools";

export default class Profile extends React.Component {
  constructor(props) {
    super(props)

    this.state = {getData: false}

    getDataElements('com.empreinte.homeData')
      .then(res => {
        this.setState({data: res[0], getData: true});
        console.log(this.state);
      })

    console.log(this.state);
  }

  componentWillMount() {

  }

  render() {
    return (
      this.state.getData === true ?
      <Page title={this.state.data.name} subtitle="subtitle">
        <div className={style.main}>
          <div>
            <div>
              <h3 className={styleRetraite.titleRetraite} >TITLE</h3>
            </div>
          </div>
          <div>
            <h3 className={styleRetraite.titleRetraite} >Coordonnées</h3>

              <div className="col-md-2">
                <h4>Civilité</h4> <div className={style.vitem}>{this.state.data.civilite}</div>
              </div>
              <div className="col-md-5">
                <h4>Nom</h4> <div className={style.vitem}>{this.state.data.name}</div>
              </div>
              <div className="col-md-5">
                <h4>Prénom</h4> <div className={style.vitem}>{this.state.data.firstname}</div>
              </div>

              <div className="col-md-12">
                <h4>Date de naissance</h4> <div className={style.vitem}>{this.state.data.birthdate.day}/{this.state.data.birthdate.month}/{this.state.data.birthdate.year}</div>
              </div>


              <div className="col-md-12">
                <h4>Voie</h4> <div className={style.vitem}>{this.state.data.address.way}</div>
              </div>
              <div className="col-md-6">
                <h4>Code Postal</h4> <div className={style.vitem}>{this.state.data.address.code}</div>
              </div>
              <div className="col-md-6">
                <h4>Ville</h4> <div className={style.vitem}>{this.state.data.address.city}</div>
              </div>

              <div className="col-md-6">
                <h4>Téléphone</h4> <div className={style.vitem}>{this.state.data.telephone}</div>
              </div>
              <div className="col-md-6">
                <h4>Email</h4> <div className={style.vitem}>{this.state.data.email}</div>
              </div>
          </div>
        </div>
      </Page> : <span>Chargement ...</span>
    );
  }
}



