import React, {Component} from 'react'
import style from '../../styles/Page.scss'
import classNames from 'classnames'
import button from '../../styles/buttons.scss'

export const Page = ({ children, actions, options, title, subtitle, onBackPress, flex, firstP }) => {
    return (
        <section className={classNames(style.main, {[style.flex]: flex})}>
            <div className={style.header}>
                <div className={style.row1}>
                    <h1 className={style.title}>{ title }</h1>
                    {onBackPress && (<button onClick={onBackPress} className={classNames(button.button, button.stable, style.back)}><i className="ion-ios-arrow-thin-left"></i></button>)}

                </div>
                <div className={style.row2}>
                    <h2 className={style.subtitle}>{ subtitle || ' ' }</h2>
                </div>
              <div className={style.row2}>
                <h3 className={style.titlePage} >{ firstP || ' ' }</h3> 
                <ul className={style.actions}>
                  { actions && actions.map((item, key) => (<li key={key}>{item}</li>)) }
                </ul>
                {options && (<ul className={style.options}>
                  { options.map((item, key) => (<li key={key}>{item}</li>)) }
                </ul>)}
              </div>
            </div>
            <div className={classNames(style.content, {[style.flex]: flex})}>
                {children}
            </div>
        </section>
    )
}

export default Page



