import { DOCTYPE_META } from '../constants'
import {DOCTYPE_F_CONTACTS} from "../constants/index";

export const getMeta = async() => {
    try {
        const indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name'])
        const result = await cozy.client.data.query(indexRef, {
            selector: {
                "name": "contactType"
            }
        })
        return result
    } catch (error) {
        return []
    }
}

export const getCustomContactMeta = async() => {
  try {
    const indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name']);
    const result = await cozy.client.data.query(indexRef, {
      selector: {
        "name": "customContactType"
      }
    })

    return result
  } catch (error) {
    return []
  }
}

export const contactExist= async(name) => {
  try {
    const indexRef = await cozy.client.data.defineIndex(DOCTYPE_F_CONTACTS, ['name'])
    const result = await cozy.client.data.query(indexRef, {
      selector: {
        "name": name
      }
    })
    console.log(result);
    return result
  } catch (error) {
    return []
  }
}




