import React from "react";
import _ from 'lodash'
import classNames from 'classnames'
import PortalWithState from 'react-portal'
import { Link } from 'react-router-dom'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from '../elements/Page'

import { getFileTypes, getTypesByGroup } from '../../scripts/fileTypes'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleContact from '../../styles/Contacts.scss'
import styleRetraite from '../../styles/Retraite.scss'
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'
import { DOCTYPE_CONTACTS, DOCTYPE_F_CONTACTS, DOCTYPE_F_CUSTOMCONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_META } from "../../constants/index";
import { getFields, getInfosFromDoctype } from "../../scripts/dataFields";
import DataForm from "../../containers/DataForm";
import { contactExist, getMeta, getCustomContactMeta } from "../../scripts/meta";
import { string_to_slug } from "../../tools/index";
import { getContactMeta } from "../../scripts/onboarding";
import customtypes from '../../constants/types.json'
import HoverImage from "react-hover-image"
import html2canvas from 'html2canvas';
import * as jsPDF  from 'jspdf'
import qs from "query-string";

export default class Mairie extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            caisse: '............................',
            numeroAdresse: '............................',
            rue: '',
            postale: '............................',
            ville: '',
            pays: '',
            telephone: '',
            telecopie: '',
            email: '',
            immatriculation: '............',
            faite: '..........................',
            recu: '',
            faitePar: '',
            error: ''
        };
        this.handleLetter = this.handleLetter.bind(this)
    }

    componentWillMount() {
      let id = qs.parse(this.props.location.search).id;

      cozy.client.data.find('io.cozy.contacts', id).then(res => {
        console.log(res);
        this.setState({data: res});
      });
    }

    printElem() {
        const input = document.getElementById('letter');
        html2canvas(input)
          .then((canvas) => {
            const dataURL  = canvas.toDataURL('image/jpeg');
            const downloadLink = document.createElement('a');
            downloadLink.href = dataURL;
            downloadLink.download = 'lettre-mairie.png';
            downloadLink.style.display = 'nones';
            document.body.appendChild(downloadLink);
            downloadLink.click();
            // console.log(imgData);
            // window.open(imgData);
            // const pdf = new pdfConverter('p', 'mm', 'a4');
            // pdf.fromHTML(window.document.getElementById('letter'), 10, 10,{'width': 180});
            // // pdf.addImage(imgData, 'JPEG', 0, 0);
            // // pdf.output('dataurlnewwindow');
            // pdf.save("download.pdf");
          })
        ;
    }

    handleLetter() {
        if (document.getElementById('caisse').value == '' || document.getElementById('numeroAddresse').value == '' || document.getElementById('rue').value == ''
            || document.getElementById('postale').value == '' || document.getElementById('ville').value == '' || document.getElementById('pays').value == ''
            || document.getElementById('telephone').value == '' || document.getElementById('telecopie').value == '' || document.getElementById('email').value == ''
            || document.getElementById('immatriculation').value == '' || document.getElementById('faite').value == '' || document.getElementById('recu').value == ''
            || document.getElementById('faitePar').value == '') {
            this.setState({
                error: "Veuillez compléter l'intégralité des informations"
            })
        } else {
            this.setState({
                caisse: document.getElementById('caisse').value,
                numeroAdresse: document.getElementById('numeroAddresse').value,
                rue: document.getElementById('rue').value,
                postale: document.getElementById('postale').value,
                ville: document.getElementById('ville').value,
                pays: document.getElementById('pays').value,
                telephone: document.getElementById('telephone').value,
                telecopie: document.getElementById('telecopie').value,
                email: document.getElementById('email').value,
                immatriculation: document.getElementById('immatriculation').value,
                faite: document.getElementById('faite').value,
                recu: document.getElementById('recu').value,
                faitePar: document.getElementById('faitePar').value,
                error: ''
            })
        }
    }

    render() {
        return (
          this.state.data ?
            <Page title="Sahra Vadrot" subtitle="Cet espace vous permet de renseigner l'ensemble des informations qui seront utiles pour prévenir les caisses de retraite ">
                <div className={style.main}>
                    <div >
                        <div>
                            <h3 className={styleRetraite.titleRetraite} >La mairie</h3>
                        </div>
                        <p className={styleRetraite.dataTxt} >
                            Il faut se rendre à la mairie du lieu du décès pour déclarer le décès. La mairie doit établir l'acte de décès.
                            Ce document, à faire établir en plusieurs exemplaires (au moins une dizaine), vous sera indispensable pour les démarches administratives et vous servira également de justificatif auprès de votre employeur si votre lien de parenté avec le
                            défunt vous ouvre droit à une absence rémunérée pour événement familial.
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            Dans le cas d’un décès dans un établissement public hospitalier, dans une clinique privée ou dans
                            une maison de retraite, la déclaration de décès est effectuée par l’établissement après constatation
                            du décès par le médecin.
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            Pour tout décès ayant lieu hors du domicile ou hors d’un établissement à caractère hospitalier (voie
                            publique, lieu de travail, à l’étranger...), la déclaration et le certificat de décès sont soumis à des
                            règles spécifiques.

                        </p>
                        <p className={styleRetraite.dataTxt} >
                        Lorsque le défunt est lié par un Pacs, il faut informer le tribunal d’instance dans les 36 heures qui
                        suivent le décès.

                        </p>
                        <div className={styleRetraite.bloc} >
                            <p>
                                éléments à fournir à la mairie
                            </p>
                        </div>
                        <div className={styleRetraite.divContact} >
                                <div className={styleRetraite.contentContact}  >
                                    <p className={styleRetraite.contactName} >Certificat médical constatant le décés</p>
                                </div>
                                <hr className={styleRetraite.seperator}/>
                            </div>
                            <div className={styleRetraite.divContact} >
                                <div className={styleRetraite.contentContact}  >
                                    <p className={styleRetraite.contactName} >Piéce d'identité du défunt</p>
                                    <p className={styleRetraite.description} >Livret de famille ou carte d'identité</p>
                                </div>
                                <hr className={styleRetraite.seperator}/>
                            </div>
                            <div className={styleRetraite.divContact} >
                                <div className={styleRetraite.contentContact}  >
                                    <p className={styleRetraite.contactName} >Piéce d'identité du déclarant</p>
                                </div>
                                <hr className={styleRetraite.seperator}/>
                            </div>
                    </div>
                </div>

              <div>
                <h3 className={styleRetraite.titleRetraite} >Coordonnées</h3>

                <div className={styleRetraite.contentForm} >
                  <div>
                    <label>Organisme</label>
                    <input type="text" id="caisse" value={this.state.data.organism} className={styleRetraite.inputForm} />
                  </div>

                  <div>
                    <label>adresse</label>
                    <div className={styleRetraite.adresse}>
                      <input type="text" id="rue" value={this.state.data.address.way} className={[styleRetraite.inputForm].join(' ')} />
                    </div>
                  </div>
                  <div>
                    <div className={styleRetraite.adresse}>
                      <input type="text" id="postale" value={this.state.data.address.code} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                      <input type="text" id="ville" value={this.state.data.address.city} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                    </div>
                  </div>
                  <div>
                    <div className={styleRetraite.labelAdresse}>
                      <label>numéro de téléphone</label>
                      <label>numéro de télécopie</label>
                      <label>email</label>
                    </div>
                    <div className={styleRetraite.adresse}>
                      <input type="text" id="telephone" value={this.state.data.telephone} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                      <input type="text" id="telecopie" value={this.state.data.telecopie} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                      <input type="text" id="email" value={this.state.data.email} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                    </div>
                  </div>

                </div>

              </div>
            </Page>
            : <div>Chargement ...</div>
        );
    }
}



