import React, {Component} from 'react'
import classNames from 'classnames'
import {Link} from 'react-router-dom'
import Portal from 'react-portal'
import _ from 'lodash'

import button from '../../styles/buttons.scss'
import style from '../../styles/Documents.scss'

import Loader from './Loader'
import Modal from '../modals/Modal'
import Page from './Page'
import DataTable from './DataTable'
import DocumentList from './DocumentList'
import Selector from '../../containers/Selector'

import { getInfosFromSlug, getFields } from '../../scripts/dataFields'

import {STATUS_LOAD, ROOT_NAME, VIEW_LIST, DOCTYPE_F_CUSTOMCONTACTS, DOCTYPE_META} from '../../constants'
import { createFakePath, isDirectory, extractFileAttributes } from '../../tools'
import confirm from '../../scripts/confirm'
import ConfirmModal from '../modals/ConfirmModal'
import {getCustomContactMeta} from "../../scripts/meta";

export default class Data extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            edition: false,
            referenced: false,
            modalOpen: false,
            excerpt: ""
        }


        this.renderData = this.renderData.bind(this)
    }

    componentWillMount () {
        this.infos = getInfosFromSlug(this.props.match.params.slug)
        this.id = this.props.match.params.id

        this.props.onLoadData(this.infos.doctype, this.id)


      this.unblock = this.props.history.block((path, action) => {
            if(this.state.edition) {
                return 'Attention, vos modifications ne seront pas sauvegardées.'
            }
        })
    }

    componentWillUnmount() {
        this.unblock && this.unblock()
    }

    componentWillReceiveProps(newProps) {
       if(this.props.current != newProps.current) {
          let fields = getFields(this.infos.doctype, newProps.current.formType)
          this.setState({excerpt: fields[0].excerpt})
            if(newProps.current._id) {
                cozy.client.data.listReferencedFiles(newProps.current)
                .then(list => Promise.all(list.map(id => cozy.client.files.statById(id))))
                .then(list => {
                    const files = list.filter(f => !f.attributes.trashed).map(extractFileAttributes)
                    this.setState({referenced: files})
                })
            }
        }
    }

    openModal() {
        this.props.onImporterClick(this.infos.path)
        this.setState({modalOpen: true})
    }

    dropRef(e) {
        e.preventDefault()
        e.stopPropagation()

        const file = e.dataTransfer.files && e.dataTransfer.files[0]
        if(!file) return

        const path = this.infos.path

        cozy.client.files.statByPath(path)
        .then(dir => {
            return this.props.fileUpload([file], dir._id)
        })
        .then(data => {
            console.log('ici', data);
            return cozy.client.data.addReferencedFiles(this.props.current, data.files[0].id)
        })
        .then(() => this.props.onLoadData(this.infos.doctype, this.id))
    }

    addRef(path) {
        cozy.client.files.statByPath(path)
        .then(file => {
            if(file.attributes.type !== 'directory') {
                return cozy.client.data.addReferencedFiles(this.props.current, file._id)
            }
        })
        .then(() => this.props.onLoadData(this.infos.doctype, this.id))
    }

    removeRef(id) {
        confirm(removeDocModal)
        .then(() => {
            return cozy.client.data.removeReferencedFiles(this.props.current, id)
        })
        .then(() => this.props.onLoadData(this.infos.doctype, this.id))
    }

    save() {
        this.setState({
            edition: false
        })

        this.props.dataSave(this.infos.doctype, this.props.current)
    }

    edit() {
        this.setState({
            edition: true
        })
    }

    remove() {
        confirm(modal)
          .then(() => {
            if(this.infos.doctype == DOCTYPE_F_CUSTOMCONTACTS){
              getCustomContactMeta()
                .then(result => {
                  let newDoc = result
                  result[0].value[0].types.map((item, key) => {
                    console.log(item.type)
                    console.log(this.props.current.formType)
                    if(this.props.current.formType == item.type){
                      console.log('sfdkjhsedfkjefkjhezfhjk')
                      newDoc[0].value[0].types.splice(key, 1)
                    }
                  })
                  cozy.client.data.update(DOCTYPE_META, result[0], newDoc[0])
                    .then(newresult => {})
                });
            }
            return this.props.dataRemove(this.infos.doctype, [this.props.current])
          })
          .then(e => {
            this.unblock && this.unblock()
            this.props.history.replace('/funerals')
          })

    }

    addMulti(type, group) {
        if(!this.state.edition) return
        const { current } = this.props

        const tmp = group.map(g => ({key: g.name, value: (g.values && g.values[0]) || ''}))
        let def = {}
        for(let e of tmp) {
            def[e.key] = e.value
        }
        if(!current[type] || !Array.isArray(current[type])) current[type] = [def]
        else current[type].push(def)

        this.forceUpdate()
    }

    removeMulti(type, key) {
        if(!this.state.edition) return
        const { current } = this.props.current

        const groups = current[type]
        if(!groups) return
        groups.splice(key, 1)

        this.forceUpdate()
    }

    renderData(save = undefined) {
      console.log(this.props.current)
        if(!this.props.current) return

        const fields = getFields(this.infos.doctype, this.props.current.formType)
        if(!fields) return

        const { current } = this.props
        return (
            <DataTable>
                {fields.map((group, keygroup) => {
                  var dataItem = []
                  return (
                    <DataTable.Group title={group.name} key={keygroup}>
                      {group.group.map((item, key) => {

                        item.map((subItem, subKey) => {
                          if(subItem.hide){
                            dataItem.push(<h3 key={subItem.name}>{current[subItem.name]} </h3>)
                          }else{
                            dataItem.push(
                              <DataTable.Item
                                key={subItem.name}
                                title={subItem.display}
                                value={current[subItem.name]}
                                type={subItem.type}
                                updatable={this.state.edition}
                                values={subItem.values}
                                onChange={e => current[subItem.name] = e}/>
                            );
                          }
                        })
                      })
                      }
                      {dataItem}
                    </DataTable.Group>
                  )
                })}
                {this.state.edition && save && (<div style={{textAlign: 'right'}}>
                    {save}
                </div>)}
            </DataTable>
        )
    }

    render() {
        const loading = this.props.status === STATUS_LOAD
        console.log(this.props.current)
        const actions = []

        const remove = (<button onClick={() => this.remove()} className={classNames(button.button, button.assertive)} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)
        actions.push(remove)

        let save = undefined
        if(this.state.edition) {
            save = (<button onClick={() => this.save()} className={classNames(button.button, button.stable)} title="Sauvegarder"><i className="fa fa-floppy-o"></i><span className={button.text}>SAUVEGARDER</span></button>)
            actions.unshift(save)
        } else {
            const edit = (<button onClick={() => this.edit()} className={classNames(button.button, button.stable)} title="Modifier"><i className="fa fa-edit"></i><span className={button.text}>MODIFIER</span></button>)
            actions.unshift(edit)
        }

        const title = (
            <span className={style.breadcrums}>
                <Link to={createFakePath(this.infos.path)}>{this.infos.name}</Link>
                {this.props.current && this.props.current.name && (
                    <strong>
                        /{this.props.current.name}
                    </strong>
                )}
            </span>
        )

        return (
            <Page title={title} subtitle={this.state.excerpt}
                actions={actions}
                onBackPress={() => this.props.history.replace('/funerals')}>

                <Loader display={loading}/>
                {!loading && this.renderData(save)}
                <Portal closeOnEsc isOpened={this.state.modalOpen} onClose={() => this.setState({modalOpen: false})}>
                    <Modal>
                        <Selector files onSelect={path => this.addRef(path)}/>
                    </Modal>
                </Portal>
            </Page>
        )
    }
}

const modal = (
    <ConfirmModal>
        <p>
            Voulez-vous supprimer cet élément ?
        </p>
    </ConfirmModal>
)

const removeDocModal = (
    <ConfirmModal>
        <p>
            Supprimer ce document ?
        </p>
    </ConfirmModal>
)



