import { connect } from 'react-redux'
import { importerClick } from '../actions'
import SecuriteSociale from '../components/letter/SecuriteSociale.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedSecuriteSociale = connect(
  mapStateToProps,
  mapDispatchToProps
)(SecuriteSociale)

export default ConnectedSecuriteSociale


