import React, {Component} from 'react'
import classNames from 'classnames'
import moment from 'moment'
import _ from 'lodash'

import Page from './Page'
import button from '../../styles/buttons.scss'
import style from '../../styles/DataForm.scss'

import CustomInput from '../utils/CustomInput'
import { ROOT_NAME } from '../../constants'
import { getInfosFromSlug, getFields } from '../../../scripts/dataFields'
import { createFakePath } from '../../tools'
import {getDataElements} from "../../tools/index";
export default class DataForm extends Component {

  constructor(props, context) {
    super(props, context)

    this.state = {
      values: [],
      hiddenType: props.hiddenType || '',
      formType: props.formType || '',
      step: props.step || 0,
      onboard: this.props.onboard || false,
      import: false
    }
  }

  componentWillMount() {

    if(this.props.onboard){
      var val = [];

      switch (this.props.step){
        case 1:
          this.infos = getInfosFromSlug("homedata")
          this.fields = getFields(this.infos.doctype)

          getDataElements(this.infos.doctype)
            .then(res => {
              if(res.length > 0) {
               val = res[0]
                this.setState({values: val, onboard: true, step: 1})
              }
            })
          break;
        case 2 || 3:
          this.infos = getInfosFromSlug("fcontacts")
          this.fields = getFields(this.infos.doctype, this.state.formType)
          val['type'] = "city_hall"
          break;
        case 3 :
          this.infos = getInfosFromSlug("fcontacts")
          this.fields = getFields(this.infos.doctype, this.state.formType)
          val['type'] = ""
          this.setState({values: val, onboard: true, step: this.props.step})
          break;
      }
    }else{
      this.infos = getInfosFromSlug(this.props.slug)
      this.fields = getFields(this.infos.doctype, this.props.formType)
      console.log(this.fields);
      console.log(this.infos);
      console.log(this.props.formType);
    }
    if(this.fields[0].import){
      this.setState({import: true})
    }
  }

  previousStep() {
    const step = this.state.step - 1
    if(step < 0) return
    this.setState({step})
  }

  nextStepSkip() {
    const questions = this.fields[0].group
    const q = questions[this.state.step]
    this.state.values[q.name] = ''
    this.nextStep()
  }

  onimport(value = false){
    this.setState({import: value})
  }
  nextStep(nb = 1) {

      //Save the new data
      if(this.state.hiddenType != "")
        this.state.values["type"] = this.state.hiddenType

      if(this.state.formType != "")
        this.state.values["formType"] = this.state.formType

      let data = this.state.values

        if(data._id){

          this.props.onSaveData(this.infos.doctype, data._id, data)
            .then((data) =>{
              if(this.state.onboard){
                let val = [];
                switch (this.state.step){
                  case 1:
                  case 2:
                    this.infos = getInfosFromSlug("fcontacts")
                    this.fields = getFields(this.infos.doctype)
                    getDataElements(this.infos.doctype)
                      .then(res => {
                        let exist = false;
                        let type = "";
                        if(res.length > 0) {
                          switch (this.state.step + 1){
                            case 2:
                              exist = _.find(res, t => t.type === "Mairie")
                              type = "Mairie"
                              break;
                            case 3:
                              exist = _.find(res, t => t.type === "Médecin Traitant")
                              type = "Médecin Traitant"
                          }
                        }
                        if(exist){
                          val = exist
                        }else{
                          val['type'] = type;
                          val['telephone'] = "";
                          val['telecopie'] = "";
                          val['email'] = "";
                          val['address'] = {
                            city: '',
                            way: '',
                            code: ''
                          };
                        }
                        this.setState({values: val, onboard: true, step: this.state.step + 1})

                      })
                    break
                  case 3:
                    this.props.close(`/home`)

                }
              }else{
                console.log(this.infos);
                this.props.closeModal(`/letter/${this.infos.slug}/${data.data._id}`)
              }
            })
        }
        else{
          this.props.onNewData(this.infos.doctype, data)
            .then((data) =>{
              if(this.state.onboard){
                let val = [];
                switch (this.state.step){
                  case 1:
                  case 2:
                    if(this.state.step + 1 == 2){
                      this.infos = getInfosFromSlug("fcontacts", "default")
                    }
                    if(this.state.step + 1 == 3){
                      this.infos = getInfosFromSlug("fcontacts", "default")
                    }
                    this.fields = getFields(this.infos.doctype)
                    getDataElements(this.infos.doctype)
                      .then(res => {
                        let exist = false;
                        let type = "";
                        if(res.length > 0) {
                          switch (this.state.step + 1){
                            case 2:
                              exist = _.find(res, t => t.type === "Mairie")
                              type = "Mairie"
                              break;
                            case 3:
                              exist = _.find(res, t => t.type === "Médecin Traitant")
                              type = "Médecin Traitant"
                          }
                        }
                        if(exist){
                          val = exist
                        }else{
                          val['type'] = type;
                          val['telephone'] = "";
                          val['telecopie'] = "";
                          val['email'] = "";
                          val['address'] = {
                            city: '',
                            way: '',
                            code: ''
                          };
                        }
                        this.setState({values: val, onboard: true, step: this.state.step + 1})

                      })
                    break
                  case 3:
                    this.props.close(`/home`)
                }
              }else{
                console.log(this.infos);
                console.log(data);
                this.props.closeModal(`/letter/${data.data.formType}/${data.data._id}`)
              }
            })
        }
  }

  change(value, name)
  {
    const values = {...this.state.values, [name]: value}
    this.setState({values})
  }

  cancel() {
    this.props.history.goBack()
  }

  render() {
    const questions = this.fields[0].group
    const header = [];
    var headercontent = []

    {if(this.state.onboard){
      switch (this.state.step){

        case 1:
          headercontent["step"] = "Etape 1"
          headercontent["title "] = "Compléter votre fiche info"
          headercontent["subtitle "] = "Il s'agit ici de rappeler vos informations d'identité basique. Même si vos proches connaissent les détails, il est toujours utile de les avoir sur l'outil."

          break;
        case 2:
          headercontent["step"] = "Etape 2"
          headercontent["title "]  = "Créer ou Importer votre contact pour la Mairie"
          headercontent["subtitle "]  = "La mairie est un organisme essentiel dans les démarches d'organisation d'obsèques\n" +
            "pour vos proches, c'est elle qui va éditer l'acte de décès."
          break;

        case 3:
          headercontent["step"] = "Etape 3"
          headercontent["title "]  = "Créer votre contact pour votre Médecin"
          headercontent["subtitle "]  = "Les coordonnées du médecin sont elles aussi essentielles.\n" +
            "C’est le médecin qui constate le décès."

      }
      header.push(

      <div key="header" className="col-md-12">
        <h3>{headercontent["step"] }</h3><br/>
        <h4>{headercontent["title "] }</h4>
        <h5>{headercontent["subtitle "] }</h5>
      </div>
      )
    }
      else {
        header.push(
          <div key="header" className="col-md-12">
            <h3>{this.fields[0].name}</h3><br/>
            <h4>{this.fields[0].description || this.props.hiddenType}</h4>
          </div>
          )
      }
    }

    return (
      <div className={style.main} onClick={e => e.stopPropagation()}>

      <div className="content">
          <div className={style.header}>
            {header}
          </div>
          {this.state.import == false && questions.map((item, key) => {
            let size = 12;
            let hide =  item[0].hide || false;
            if(12 % item.length == 0 && !hide ){
              size = 12 / item.length
            }
            let row = [];
            item.map((subitem, subkey) => {
             let Class = "col-sm-12 col-md-"+size;

              if(hide){
                subitem.values == this.props.hiddenType;
                Class = style.hidden
              }
              row.push(<div className={Class}>
                <label>{subitem.question || subitem.display}</label>
                <CustomInput className={style.input}  value={this.state.values[subitem.name]}  type={subitem.type} values={subitem.values} placeholder={subitem.display} onChange={e => this.change(e, subitem.name)} /></div>)
            })
            return(row);
          })}
          {this.state.import == true &&
            <div>
              <div className={classNames("col-md-6", style.importCard)} onClick={() => this.onimport()}>
                <i className={"ion-ios-person"}></i> <br/>

                Créer votre contact <br/>
                <span>

                </span>
              </div>
              <div className={classNames("col-md-6", style.importCard, style.disable)}>
                <div className={style.overlay}>

                </div>
                <i className={classNames( "ion-ios-people")}></i> <br/>

                Importer votre contact <br/>
                <span>

                </span>
              </div>
            </div>
          }
        </div>
        <div className={style.footer}>
          <button onClick={() => this.props.close()} className={classNames(style.button, button.button, button.defaultLight)} title="Fermer">
            FERMER
          </button>
          {
            this.state.import == false &&
            <button onClick={() => this.nextStep()} className={classNames(style.button, button.button, button.default)} title="Suivant">
              VALIDER
            </button>
          }
        </div>

      </div>
    )
  }
}



