import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import button from '../../styles/buttons.scss'
import style from '../../styles/Contacts.scss'
import ToolBoxOptions from './ToolBoxOptions'
import _ from 'lodash'
import { Link } from 'react-router-dom'

import Page from './Page'
import Loader from './Loader'
import ContactList from './ContactList'

import Modal from '../modals/Modal'
import ContactsModal from '../modals/ContactsModal'
import ReactTooltip from 'react-tooltip'

import { VIEW_LIST, STATUS_LOAD, STATUS_DONE } from '../../constants'

export default class Contacts extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            current: null,
            modalOpen: false,
            edition: false
        }

        this.idDetected = false
    }

    componentWillMount () {
        this.props.onLoadContacts()
    }

    componentWillReceiveProps (newProps) {
        if(newProps.elements.length !== newProps.count) return newProps.onLoadContacts()

        if(this.props.match.params.id && !this.idDetected && newProps.elements.length > 0) {
            //Compute id
            this.idDetected = true
            for(const e of newProps.elements) {
                if(e._id === this.props.match.params.id) {
                    this.setState({
                        current: e,
                        modalOpen: true
                    })
                    break
                }
            }
        }
    }

    add() {
        this.setState({
            current: null,
            modalOpen: true,
            edition: true
        })
    }

    open(item) {
        this.setState({
            current: item,
            modalOpen: true
        })
    }

    select(item) {
        this.props.onSelectContact(item)
    }

    remove() {
        if(!this.state.current) return
        this.props.onRemoveContacts([this.state.current])
        this.setState({
            modalOpen: false,
            current: null
        })
    }

    addOrSaveContact(contact) {
        if(this.state.current) {
            this.props.onSaveContact(this.state.current._id, contact)
        } else {
            this.props.onNewContact(contact.firstName, contact)
        }
        this.setState({
            modalOpen: false,
            current: null
        })
    }

    render() {
        cozy.client.data.findAll("com.empreinte.contacts")
        .then((index) => {
          console.log(index)
        })
        const { elements, count, selectedCount, status } = this.props

        const loading = status === STATUS_LOAD

        const add = (<button onClick={() => this.add()} className={classNames(button.button, button.default)}><img className={style.add}  src="public/media/add.svg" />AJOUTER UN PROCHE</button>)
        const toolbox = (
            <ToolBoxOptions>
                <ul>
                    <li onClick={() => {
                        this.props.onSelectAll()
                    }}>
                        <i className="ion-ios-checkmark-outline">TOUT SELECTIONNER</i>
                    </li>

                </ul>
            </ToolBoxOptions>
        )
        const remove = (<button onClick={() => this.props.onRemoveContacts(elements.filter(f => f.selected))} className={classNames(button.button, button.assertive)}><img className={style.add}  src="public/media/add.svg" />SUPPRIMER</button>)

        const actions = [add]
        if(selectedCount > 0)
                actions.unshift(remove)

        let list = undefined
        if(!loading && elements.length > 0) {
            const sort = elements.sort((a, b) => {
                if(a.lastName < b.lastName) return -1
                if(a.lastName > b.lastName) return 1
                return 0
            })

            const sortedContacts = _.groupBy(sort, item => {
                let name = item.lastName || item.firstName || item.company || '_'
                name = name.trim().toUpperCase()

                const firstLetter = name[0]

                if ((/[0-9]/g).test(firstLetter)) {
                    return '#'
                }
                return firstLetter
            })

            const keys = Object.keys(sortedContacts)
            const sortedKeys = keys.sort((a,b) => {
                if(a < b) return -1
                if(a > b) return 1
                return 0
            })

            list = []

            for(let letter of sortedKeys) {
                const peoples = sortedContacts[letter]
                list.push(
                    <div key={letter}>
                        <ContactList view={VIEW_LIST} onClickItem={item => this.open(item)} onSelectItem={item => this.select(item)}>
                            {peoples}
                        </ContactList>
                    </div>
                )
            }
            const sharing = cozy.client.files.getCollectionShareLink("dd9d4d75f37d781adf3b0c2b5c3434a2", 'com.empreinte.homeData')
            console.log(sharing)
        }

        return (
            <Page title="Mes proches" subtitle="Titulaire du compte ou proche désigné par celui-ci, vous avez accès aux mêmes informations, mais pas en même temps. Le premier remplit le dossier selon ses choix intimes, renseigne les contacts, désigne les intervenants... Quelque part, il met en scène son départ. Quant au second, il suit les indications, « déroule le scénario » sereinement et accompagne l’application des décisions. Un lien fort se concrétise dans cet Espace privé.">
              <Loader display={loading}/>
              <div className={style.row}>
                <h3 className={style.titlePage} data-type="light" data-tip="Lorem Ipsum" >Mes proches référents <img src="public/media/information.svg" className={style.information} /></h3> 
                    <ul className={style.actions}>
                    { actions && actions.map((item, key) => (<li key={key}>{item}</li>)) }
                    </ul>
              </div>
            <div className={style.row} >
                <p className={style.introProches}  >
                    Le titulaire du compte désigne un ou plusieurs de ses proches qui auront accès aux données après le décès. Ils les utiliseront pour organiser les obsèques selon les choix et pourront s'acquitter aisément de l'ensemble des démarches administratives. Le titulaire désigne cette ou ces personne(s) de confiance ici
                </p>
            </div>
            <div className={style.dataReferent}  >
                {list}
            </div>

              <div className={style.row}>
                <h3 className={style.titlePage} data-type="light" data-tip="Ce dossier s’adresse aux proches référents. Il contient les dernières volontés du titulaire et un assistant administratif qui hiérarchise et automatise les démarches. " >Mon dossier obsèques<img src="public/media/information.svg" className={style.information} /></h3> 
              </div>
            <div className={style.row} >
                <p className={style.introProches}  >
                    Ce dossier s’adresse aux proches référents. Il contient les dernières volontés du titulaire et un assistant administratif qui hiérarchise et automatise les démarches.
                </p>
            </div>
            <div className={style.dataObseque}  >
            <Link to="/dossier">
              <div className={style.divContact} >
                <div className={style.contentContact}  >
                    <p className={style.contactName} >Sahra Vadrotte</p>
                    <img  className={style.chevron} src="public/media/chevronpurple.svg" />
                </div>
                <hr className={style.seperator}/>
              </div>
            </Link>
            </div>

              <div className={style.row}>
                <h3 className={style.titlePage} data-type="light" data-tip="Le titulaire entre ici la liste des personnes qui seront prévenues de son décès. Notamment celles dont les coordonnées ne sont peut-être pas connues des proches référents (voisin, ami d’enfance, femme de ménage …). " >Mes autres proches à prévenir <img src="public/media/information.svg" className={style.information} /></h3> 
                    <ul className={style.actions}>
                    { actions && actions.map((item, key) => (<li key={key}>{item}</li>)) }
                    </ul>
              </div>
            <div className={style.row} >
                <p className={style.introProches}  >
                    Le titulaire entre ici la liste des personnes qui seront prévenues de son décès. Notamment celles dont les coordonnées ne sont peut-être pas connues des proches référents (voisin, ami d’enfance, femme de ménage …). 
                </p>
            </div>
            <div className={style.dataPrevenir}  >
              {/* <div className={style.divContact} >
                <div className={style.contentContact}  >
                    <p className={style.contactName} >Alban Panpan</p>
                    <img  className={style.chevron} src="public/media/chevronpurple.svg" />
                </div>
                <hr className={style.seperator}/>
              </div>
              <div className={style.divContact} >
                <div className={style.contentContact}  >
                    <p className={style.contactName} >Wandrille le Nil</p>
                    <img  className={style.chevron} src="public/media/chevronpurple.svg" />
                </div>
                <hr className={style.seperator}/>
              </div> */}
            </div>
              <ReactTooltip />
              {/* {!loading && elements.length === 0 && "Aucun contact"} */}

              <Portal isOpened={this.state.modalOpen} closeOnEsc onClose={() => this.setState({modalOpen: false, current: null, edition: false})}>
                <Modal>
                  <ContactsModal
                    item={this.state.current}
                    title={this.state.current ? "Modifier votre contact" : "Créer votre contact"}
                    onSave={contact => this.addOrSaveContact(contact)}
                    onRemove={() => this.remove()}
                    edit={this.state.edition}/>
                </Modal>
              </Portal>

            </Page>
        )
    }
}



