import { connect } from 'react-redux'
import { importerClick, importerFilesUpload, loadFolder } from '../actions'
import Devis from '../components/elements/Devis.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    },
    onUploadFile: (files, DirId) => {
      dispatch(importerFilesUpload(files, DirId));
    },
    onLoadFolder: (path) => {
      dispatch(loadFolder(path));
    }
  
  }
}

const ConnectedDevis = connect(
  mapStateToProps,
  mapDispatchToProps
)(Devis)

export default ConnectedDevis


