import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Bailleur from '../components/letter/Bailleur.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedBailleur = connect(
  mapStateToProps,
  mapDispatchToProps
)(Bailleur)

export default ConnectedBailleur


