import contactFields from '../constants/data/contact.json'
import diagnosisFields from '../constants/data/diagnosis.json'
import homeFields from '../constants/data/home.json'
import equipmentFields from '../constants/data/equipment.json'
import funeralsFields from '../constants/data/funerals/contact.json'

import {
  DATA_LIST, DOCTYPE_HOMEDATA, DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_WORKS, DOCTYPE_MAINTENANCES,
  DOCTYPE_DPE, DOCTYPE_F_CUSTOMCONTACTS
} from '../constants'
import {DOCTYPE_F_CONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_F_YOUNES} from "../constants/index";
import lastWillFields from '../constants/data/funerals/last_will.json'

export const getContactFields = () => {
    return contactFields
}

export const getDiagnosisFields = () => {
    return diagnosisFields
}

export const getHomeFields = () => {
    return homeFields
}

export const getEquipmentFields = () => {
    return equipmentFields
}


export const getFields = (doctype, type = "default") => {
    switch (doctype) {
      case DOCTYPE_DIAGNOSIS:
        return diagnosisFields
      case DOCTYPE_EQUIPMENTS:
        return equipmentFields
      case DOCTYPE_HOMEDATA:
        return homeFields
      case DOCTYPE_F_LASTWILL:
        return lastWillFields
      case DOCTYPE_F_CONTACTS:
      case DOCTYPE_F_CUSTOMCONTACTS:
        switch (type){
          case "work":
            return funeralsFields.work
          case "work_help":
            return funeralsFields.work_help
          case "bank":
            return funeralsFields.bank
          case "credit":
            return funeralsFields.credit
          case "health_organism":
            return funeralsFields.health_organism
          case "home_help":
            return funeralsFields.home_help
          case "cpam":
            return funeralsFields.cpam
          case "assurance_material":
            return funeralsFields.assurance_material
          case "assurance_person":
            return funeralsFields.assurance_person
          case "notaire":
            return funeralsFields.notaire
          case "retraite":
            return funeralsFields.retraite
          case "bailleur":
            return funeralsFields.bailleur
          case "locataire":
            return funeralsFields.locataire
          case "caf":
            return funeralsFields.caf
          case "organism_payer":
            return funeralsFields.organism_payer
          case "impot":
            return funeralsFields.impot
          default:
            return funeralsFields.default
        }
    }
}

export const getInfosFromPath = path => {
    for(const data of DATA_LIST) {
        if(data.path === path) return data
    }
}

export const getInfosFromSlug = slug => {
    for(const data of DATA_LIST) {
        if(data.slug === slug) return data
    }
}

export const getInfosFromDoctype = doctype => {
    for(const data of DATA_LIST) {
        if(data.doctype === doctype) return data
    }
}



