import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Complementaire from '../components/letter/Complementaire.jsx'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
  return {
    usedType: state.gauge.usedTypes,
    dataCreated: state.gauge.dataCreated,
    status: state.gauge.status
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onFileClick: (path, type) => {
      dispatch(importerClick(path, type))
    }
  }
}

const ConnectedComplementaire = connect(
  mapStateToProps,
  mapDispatchToProps
)(Complementaire)

export default ConnectedComplementaire


