import React from "react";

import Page from '../elements/Page'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleRetraite from '../../styles/Retraite.scss'

import html2canvas from 'html2canvas';
import data from '../../constants/data/share.json';
import {getDataElements} from "../../../tools";

export default class LastWill extends React.Component {
  constructor(props) {
    super(props)

    this.state = {getData: false}

    getDataElements('com.empreinte.FLastWill')
      .then(res => {
        this.setState({data: res[0], getData: true});
        console.log(this.state);
      })

    console.log(this.state);
  }

  componentWillMount() {

  }

  render() {
    return (
      this.state.getData === true ?
        <Page title="TITLE" subtitle="subtitle">
          <div className={style.main}>
            <div>
              <div>
                <h3 className={styleRetraite.titleRetraite} >TITLE</h3>
              </div>
            </div>
            <div>
              <h3 className={styleRetraite.titleRetraite} >Ses dernières volontées</h3>

              <div className="col-md-12">
                <h4>Les Soins de Conservation</h4> <div className={style.vitem}>{this.state.data.conservation}</div>
              </div>

              <div className="col-md-12">
                <h4>Les choix du cerceuil et de ses accessoires</h4> <div className={style.vitem}>{this.state.data.cercueil}</div>
              </div>

              <div className="col-md-12">
                <h4>La présentation du corps</h4> <div className={style.vitem}>{this.state.data.presentation}</div>
              </div>

              <div className="col-md-12">
                <h4>L’Organisation des funérailles</h4> <div className={style.vitem}>{this.state.data.organisation}</div>
              </div>

              <div className="col-md-12">
                <h4>Cérémonial</h4> <div className={style.vitem}>{this.state.data.ceremonial}</div>
              </div>

              <div className="col-md-12">
                <h4>Crémation ou Inhumation</h4> <div className={style.vitem}>{this.state.data.incineration}</div>
              </div>

              <div className="col-md-12">
                <h4>Quel devenir pour les cendres ?</h4> <div className={style.vitem}>{this.state.data.ifIncineration}</div>
              </div>

              <div className="col-md-12">
                <h4>Souhaits Particuliers</h4> <div className={style.vitem}>{this.state.data.wish}</div>
              </div>


            </div>
          </div>
        </Page> : <span>Chargement ...</span>
    );
  }
}



