import React from 'react'
import Page from "./elements/Page";
import style from "../styles/AssistanceList.scss";
import classNames from 'classnames'

const AssistanceList = (props) => {
  return (
    <Page title="Services MAIF"
      subtitle="Retrouvez tous les services du contrat obsèques MAIF pour vous et vos proches.">

      <div className={style.row}>
        <h3 className={style.titleStep} >
          Service « Aide juridique »
        </h3>
        <div className={style.content}>
          <p>
            Ce service téléphonique d’informations juridiques, inclus dans le contrat Sollicitudes donnent des
            informations sur la succession (donation, testament) et la protection des personnes dépendantes vivant au
            domicile de l’adhérent.
            <div className={style.separator}></div>
            Ce service est ouvert pour l’adhérent Sollicitudes mais aussi pour les proches au moment et après le décès.
            <div className={style.separator}></div>
            Ce service fonctionne du lundi au vendredi de 8h30 à 17h30.
            <div className={style.separator}></div>
            Un n° de téléphone direct est affiché pour l’adhérent Sollicitudes.
          </p>
        </div>

      </div>

      <div className={style.row}>
        <h3 className={style.titleStep} >
          Service « Soutien moral »
        </h3>
        <div className={style.content}>
          <p>
            Le service de soutien moral, inclus dans le contrat Sollicitudes, est à la disposition du proche pour
            l’aider à surmonter l’épreuve de deuil.
            <div className={style.separator}></div>
            Il s’agit d’un accompagnement téléphonique dans les premières manifestations normales et habituelles du
            deuil.
            <div className={style.separator}></div>
            Le bénéficiaire peut, s’il le souhaite, être orienté vers un professionnel de santé et/ou une structure
            spécialisée dans un tel accompagnement.
            <div className={style.separator}></div>
            Le soutien prend la forme de trois entretiens téléphoniques et est accessible au conjoint ou partenaire de
            pacs ou concubin ou une personne vivant en permanence au domicile de l’adhérent défunt.
            <div className={style.separator}></div>
            Le service est accessible durant les 90 jours consécutifs au décès.
          </p>
        </div>

      </div>
      <div className={style.row}>
        <h3 className={style.titleStep}  >
          Service « Aides pratique et sociale »
        </h3>
        <div className={style.content}>
          <div className={style.cardGroup}>
            <div className={classNames(style.card, "col-sm-3")}>
              <div className={style.icon}>
                <img className={style.pharmacie} src="public/media/Pharmacie.svg" />
              </div>
              <h4>Médical</h4>
            </div>
            <div className={classNames(style.card, "col-sm-3")}>
              <div className={style.icon}>
                <img className={style.ambulance} src="public/media/Ambulance.svg" />
              </div>
              <h4>Rapatriement</h4>
            </div>
            <div className={classNames(style.card, "col-sm-3")}>
              <div className={style.icon}>
                <img src="public/media/Calendar.svg" />
              </div>
              <h4>Quotidien</h4>
            </div>
            <div className={classNames(style.card, "col-sm-3")}>
              <div className={style.icon}>
                <img src="public/media/signature.svg" />
              </div>
              <h4>Administratif</h4>
            </div>
          </div>
          <div className={style.subRow}>
            <div className="col-md-1">
              <div className={style.icon}>
                <img className={style.iconList} src="public/media/PharmacieP.svg" />
              </div>
            </div>
            <div className="col-md-11">
              <div className={style.content}>
                <h3>
                  Médical
                </h3>
                <p>
                  Ce service, inclus dans le contrat Sollicitudes, téléphonique vous permet d’obtenir des informations
                  dans les domaines suivants :
                </p>
                <ul>
                  <li>• Mieux comprendre une ordonnance, un terme médical</li>
                  <li>• Savoir à quoi correspond un symptôme et définir le degré d’urgence d’une visite médicale,</li>
                  <li>• Déterminer ce qu’il convient de faire en attendant un médecin,</li>
                  <li>• Évoquer des préoccupations personnelles</li>
                </ul>
                <p>
                  Ce service est accessible du lundi au vendredi, de 8h30 à 17h30
                </p>
              </div>
            </div>
          </div>
          <div className={style.subRow}>
            <div className="col-md-1">
              <div className={style.icon}>
                <img className={style.iconListAmbulance} src="public/media/AmbulanceP.svg" />
              </div>
            </div>
            <div className="col-md-11">
              <div className={style.content}>
                <h3>
                  Rapatriement
                </h3>
                <p>
                  Ce service, inclus dans le contrat Sollicitudes, comprend les prestations suivantes : </p>
                <ul>
                  <li>• Rapatriement de l’adhérent défunt jusqu’au lieu d’inhumation suite à déplacement à l’étranger.
                  </li>
                  <li>• Transport de l’adhérent défunt, lorsque le décès survient en France à plus de 50 kms du domicile
                    de l’adhérent.
                  </li>
                  <li>• Retour des accompagnateurs de l’adhérent défunt s’il s ne peuvent pas revenir par leurs propres
                    moyens.
                  </li>
                  <li>• Déplacement d’un proche sur le lieu du décès en cas de nécessité réglementaire si rapatriement.
                  </li>
                  <li>• Prise en charge des frais de préparation de l’adhérent défunt, aménagements transports, cercueil
                    adapté.
                  </li>
                </ul>
                <p>
                  Accessible 24h/24 et tout au long de l’année, sur simple appel téléphonique,
                </p>
              </div>
            </div>
          </div>

          <div className={style.subRow}>
            <div className="col-md-1">
              <div className={style.icon}>
                <img className={style.iconList} src="public/media/CalendarP.svg" />
              </div>
            </div>
            <div className="col-md-11">
              <div className={style.content}>
                <h3>
                  Quotidien
                </h3>
                <p>
                  Ce service , inclus dans le contrat Sollicitudes, prévoir la mise en place d’aides diverses pour accompagner la famille de l’adhérent à faire face aux difficultés au moment du décès :
                </p>
                <ul>
                  <li>
                  • Aide dans les tâches domestiques quotidiennes pour aider le conjoint de l’adhérent défunt.
                  </li>
                  <li>T
                  • Aide au transport du conjoint non autonome.
                  </li>
                  <li>• Retour des accompagnateurs de l’adhérent défunt s’il s ne peuvent pas revenir par leurs propres
                    moyens.
                  </li>
                  <li>
                  • Téléassistance ou garde à domicile pour une personne dépendante
                  </li>
                  <li>
                  • Garde à domicile et accompagnement à l’école de l’enfant de moins de 15 ans
                  </li>
                  <li>
                    • Prise en charge de l’organisation du transport et la pension animalière du chien ou chat
                  </li>
                </ul>
                <p>
                • Accessible pendant les 21 jours consécutifs au décès de l’adhérent et plafonné à 350€.
                </p>
              </div>
            </div>
          </div>
          <div className={style.subRow}>
            <div className="col-md-1">
              <div className={style.icon}>
                <img className={style.iconList} src="public/media/signatureP.svg" />
              </div>
            </div>
            <div className="col-md-11">
              <div className={style.content}>
                <h3>
                  Administratif
                </h3>
                <p>
                  Ce service, inclus dans le contrat Sollicitudes, propose de réaliser par téléphone un diagnostic de la situation administrative du défunt pour faciliter la prise en charge du conjoint, du partenaire de pacs ou du concubin ou du représentant de la famille.
                  <div className={style.separator}></div>
                  Ce service est accessible pendant les 180 jours consécutif au décès de l’adhérent.</p>
              </div>
            </div>
          </div>
        </div>
      </div>

    </Page>
  )


}
export default AssistanceList



