import React from "react";
import _ from 'lodash'
import classNames from 'classnames'
import PortalWithState from 'react-portal'
import { Link } from 'react-router-dom'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from './Page'

import { getFileTypes, getTypesByGroup } from '../../scripts/fileTypes'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleNav from '../../styles/Navigation.scss'
import stylePresta from '../../styles/Prestataire.scss'
import styleDon from '../../styles/Don.scss';
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'
import { DOCTYPE_CONTACTS, DOCTYPE_F_CONTACTS, DOCTYPE_F_CUSTOMCONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_META } from "../../constants/index";
import { getFields, getInfosFromDoctype } from "../../scripts/dataFields";
import DataForm from "../../containers/DataForm";
import { contactExist, getMeta, getCustomContactMeta } from "../../scripts/meta";
import { string_to_slug } from "../../tools/index";
import { getContactMeta } from "../../scripts/onboarding";
import customtypes from '../../constants/types.json'
import HoverImage from "react-hover-image"
import ReactTooltip from 'react-tooltip'

export default class Don extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            open: false,
            current: '',
            hiddenType: '',
            search: '',
            typeGroup: [],
            customTypeGroup: [],
            TypeOpen: false,
            InfoOpen: false,
            infoHeader: "",
            infoText: "",
            firstname: "",
            formType: "",
            newTypeType: "",
            lastname: "",
            link: ''
        };
    }

    render() {
        return (
            <Page title="Préparer mes obsèques" subtitle="Cet espace vous permet de renseigner l'ensemble des informations qui seront utiles à vos proches. ">
                <div className={style.main}>
                    <div className={style.header}>
                        <div className={stylePresta.titlePresta} >
                            <h2 className={styleDon.titleStep}>Don du corps à la science</h2>
                        </div>
                    </div>
                    <div className={stylePresta.stepDiv}>
                        <p>Etape 1</p>
                    </div>
                    <div className={stylePresta.contentStep} data-type="light" data-tip="Donner son corps est payant : les frais de transport coûtent entre 200 à 900 euros, réglés d'avance)." >
                        <a href="http://www.afif.asso.fr/francais/conseils/conseil15.html" target="_blank" >
                            <h3>Trouver une faculté de médecine <img  className={styleNav.chevron} src="public/media/chevronpurple.svg" /> </h3>
                            <p>La démarche doit s'effectuer auprès de la faculté de médecine dont dépend votre domiciliation. Vous devrez notamment fournir une lettre de demande manuscrite.</p>
                        </a>
                    </div>

                    <div className={stylePresta.stepDiv}>
                        <p>Etape 2</p>
                    </div>
                    <div className={stylePresta.contentStep} data-type="light" data-tip="Il est important de noter que les cendres sont rarement rendues à la famille, le corps non plus a fortiori. Préparer vos proches à cette situation peut être utile." >
                        <a href="https://medecine.univ-amu.fr/fr/procedure-faire-don-son-corps-a-science" target="_blank" >
                            <h3>Ecrire votre lettre manuscrite <img  className={styleNav.chevron} src="public/media/chevronpurple.svg" /> </h3>
                            <p>Vous trouverez ici un modèle de lettre que vous devrez recopier sur une feuille libre et la transmettre à la faculté dont vous dépendez. </p>
                        </a>
                    </div>

                    <div className={stylePresta.stepDiv}>
                        <p>Etape 3</p>
                    </div>
                    <div className={stylePresta.contentStep}  >
                        <Link to="/devis-don">
                            <h3>Scanner et stocker dans cet espace <img  className={styleNav.chevron} src="public/media/chevronpurple.svg" /> </h3>
                            <p>Ici, vous pouvez stocker la carte de donateur que vous aura remise à la faculté. Une façon de sécuriser la démarche, car la perte du document rend impossible la donation.</p>
                        </Link>
                    </div>

                    <div className={style.header}>
                        <div className={stylePresta.titlePresta} >
                            <h2 className={styleDon.titleStep} >Refus du don d'organe</h2>
                        </div>
                    </div>
                    <div className={stylePresta.contentStep}>
                        <a href="https://www.registrenationaldesrefus.fr/#etape-1" target="_blank" >
                            <h3>Le registre National Des Refus<img  className={styleNav.chevron} src="public/media/chevronpurple.svg" /> </h3>
                            <p>Lorem Ipsum dolor sit amen Lorem ipsum dolor sit amen</p>
                        </a>
                    </div>
                    <div className={stylePresta.contentStep}>
                        <Link to="/devis-don">
                            <h3>Scanner et stocker dans cet espace <img  className={styleNav.chevron} src="public/media/chevronpurple.svg" /> </h3>
                            <p>Ici, vous pouvez stocker la confirmation d’inscription du registre national du refus. Elle contient un numéro de dossier à conserver impérativement pour toute demande ultérieure de modification ou d’annulation de votre inscription</p>
                        </Link>
                    </div>
                </div>
                <ReactTooltip />
            </Page>
        );
    }
}


