import React from "react";

import Page from '../elements/Page'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleRetraite from '../../styles/Retraite.scss'

import DataTable from './DataTable'

import classNames from 'classnames'

import html2canvas from 'html2canvas';
import data from '../../constants/data/share.json';
import {getDataElements} from "../../../tools";
import {getFields} from "../../../scripts/dataFields";

export default class Letter extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      organism: '',
      way: '',
      code: '',
      city: '',
      user: []
    };
  }

  componentWillMount() {
    this.type = this.props.match.params.slug;

    getDataElements('com.empreinte.homeData').then(res => {
      console.log(res[0]);
      console.log(this.state);
      this.setState({user: res[0], firstname: res[0].firstname,
        lastname: res[0].name,
        address: res[0].address.way + " " + res[0].address.code + " " + res[0].address.city});
      console.log(this.state);
    })

    let doctype = this.type === 'custom' ? 'com.empreinte.Fcustomcontacts' : 'io.cozy.contacts';
    console.log(doctype);
    cozy.client.data.find(doctype, this.props.match.params.id).then(res => {
      this.setState({
        data: res
      });
    });


    console.log(this.state);
  }

  save() {
    this.setState({
      edition: false
    })

    this.props.dataSave(this.infos.doctype, this.props.current)
  }

  edit() {
    this.setState({
      edition: true
    })
  }


  printElem() {
    const input = document.getElementById('letter');
    html2canvas(input)
      .then((canvas) => {
        const dataURL  = canvas.toDataURL('image/jpeg');
        const downloadLink = document.createElement('a');
        downloadLink.href = dataURL;
        downloadLink.download = 'lettre-association.png';
        downloadLink.style.display = 'nones';
        document.body.appendChild(downloadLink);
        downloadLink.click();
      });
  }

  myFormsSuivit() {

      return (
        <span>
          <h3 className={styleRetraite.titleRetraite} >Suivi de dossier</h3>
          <div className={styleRetraite.labelDemarche}>
              <label>Démarche faite le </label>
              <label>Acte reçu le </label>
          </div>
          <div className={styleRetraite.adresse}>
              <input onChange={e => this.refs.date.innerHTML = e.target.value} type="text" id="faite" placeholder="12/12/2017" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
              <input onChange={e => this.refs.deces.innerHTML = e.target.value} type="text" id="recu" placeholder="13/12/2017" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
          </div>

          <h3 className={styleRetraite.titleRetraite} >Référent</h3>
          <div className={styleRetraite.labelDemarche}>
              <label>Démarche faite par </label>
              <label>Lien de parenté</label>
          </div>
          <div className={styleRetraite.adresse}>
              <input onChange={e => this.refs.nameRef.innerHTML = e.target.value} type="text" id="faitePar" placeholder="Mr Dupont Pierre" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
              <input onChange={e => this.refs.parent.innerHTML = e.target.value} type="text" id="faite" placeholder="Frère" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
          </div>
          <div>
            <label>Adresse</label>
            <div className={styleRetraite.adresse}>
              <input type="text" id="rue" placeholder="rue"
                     className={[styleRetraite.inputForm, styleRetraite.sizeRue].join(' ')}
                     onChange={e => this.refs.wayref.innerHTML = e.target.value}/>
            </div>
          </div>
          <div>
            <div className={styleRetraite.adresse}>
              <input type="text" id="postale" placeholder="CodePostale"
                     className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')}
                     onChange={e => this.refs.coderef.innerHTML = e.target.value}/>
              <input type="text" id="ville" placeholder="Ville"
                     className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')}
                     onChange={e => this.refs.cityref.innerHTML = e.target.value}/>
            </div>
          </div>
        </span>
      )

  }

  myHeaderLetter() {
      return (
        <span>
              <div className={styleRetraite.expediteur}>
                <p ref="nameRef">Nom prénom</p>
                <p ref="wayref">Adresse</p>
                <p ref="coderef">Code</p>
                <p ref="cityref">Ville</p>
              </div>
              <div className={styleRetraite.ToCaisse}>
                <p ref="organism">{this.state.data.organism}</p>
                <p ref="way">{this.state.data.address.way}</p>
                <p><span ref="code"> {this.state.data.address.code}  </span> <span ref="city">{this.state.data.address.city}  </span>FRANCE</p>
                <p className={styleRetraite.date}>Le <span ref="date">jj/mm/yyyy</span></p>
              </div>
      </span>
      )
  }

  myLetterButton() {
    let slug = this.props.match.params.slug;
    if (data.hasOwnProperty(slug) === false) return null;
    return (
      <span>
          <div className="col-md-8">
          <h3 className={styleRetraite.titleRetraite}>Courrier type</h3>
            </div>
              <div className="col-md-4">
          <button className={styleRetraite.buttonCustom} onClick={this.printElem}>
            IMPRIMER
          </button>
</div>
          <br/><br/><br/>
        <h3> {this.state.error} </h3>
      </span>
    )
  }

  renderData() {
    this.type = this.props.match.params.slug;

    let doctype = this.type === 'custom' ? 'com.empreinte.Fcustomcontacts' : 'io.cozy.contacts';
    const fields = getFields(doctype, this.type)
    if (!fields) return

    console.log(fields);

    if (!this.state.data) return;

    return (
      <span>
        {
          fields[0].group.map(item => {
            console.log(item);
            if (item[0].hasOwnProperty('hide') === false) {
              if (item[0].type == 'address') {
                return (
                  <span>
                <div>
                  <label>Adresse</label>
                  <div className={styleRetraite.adresse}>
                    <input type="text" id="rue" placeholder={this.state.data[item[0].name].way}
                           className={[styleRetraite.inputForm, styleRetraite.sizeRue].join(' ')}
                           onChange={e => this.refs.way.innerHTML = e.target.value}/>
                  </div>
                </div>
                <div>
                  <div className={styleRetraite.adresse}>
                    <input type="text" id="postale" placeholder={this.state.data[item[0].name].code}
                           className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')}
                           onChange={e => this.refs.code.innerHTML = e.target.value}/>
                    <input type="text" id="ville" placeholder={this.state.data[item[0].name].city}
                           className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')}
                           onChange={e => this.refs.city.innerHTML = e.target.value}/>
                  </div>
                </div>
              </span>
                )
              }
              else if (item[0].type == 'interlocuteur') {
                return (
                  <span>
                <div>
                  <label>INTERLOCUTEUR</label>
                  <div className={styleRetraite.adresse}>
                    <input type="text" id="postale" placeholder={this.state.data[item[0].name].firstname}
                           className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')}
                           onChange={e => this.refs.firstname.innerHTML = e.target.value}/>
                    <input type="text" id="ville" placeholder={this.state.data[item[0].name].lastname}
                           className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')}
                           onChange={e => this.refs.lastname.innerHTML = e.target.value}/>
                  </div>
                </div>
              </span>
                )
              }
              else {
                return (
                  <span>
                    <label>{item[0].display}</label>
                    <input type="text" id={item[0].name} placeholder={this.state.data[item[0].name]}
                           onChange={e => this.refs[item[0].name].innerHTML = e.target.value}
                           className={styleRetraite.inputForm}/>
                  </span>
                )
              }
            }
          })
        }
        {this.state.getData = true}
    </span>
    )
  }

  renderLetter() {
    if (this.state.hasOwnProperty('address') === false) return null;
    if (this.state.hasOwnProperty('getData') === false) return null;
    let slug = this.props.match.params.slug;
    const d = this.state.data;
    console.log(d);

    if (data.hasOwnProperty(slug) === false) return null;

    return (
      <span>
        <page className={styleRetraite.afour} id='letter'>
          {this.myHeaderLetter()}
          <div className={styleRetraite.contentLetter}>

            <p>Madame, Monsieur,</p>
            <p>Suite au décès de {this.state.user.civilite} {this.state.user.firstname} {this.state.user.name}, mon/ma <span
              ref="parent">...</span>, survenu le <span ref="deces">jj/mm/yyyy</span>,
              {
                Object.keys(data[slug]).map(function (keyName, keyIndex) {
                  // use keyName to get current key's name
                  // and a[keyName] to get its value
                  console.log(keyName);
                  if (d[keyName] === "address") {
                    return (
                    <span>{this.state.address}</span>
                    )
                  }
                  else if (d[keyName] === "notaire") {
                    const fields = getFields('io.cozy.contacts', 'notaire')
                    console.log(fields);
                    if (!fields) return null;
                    return (
                      <span></span>
                    )
                  }
                  else if (d[keyName] === "name") {
                    return (
                      <span>{this.state.firstname} {this.state.name} </span>
                    )
                  }
                  else {

                    return(
                    <span>
                  <span dangerouslySetInnerHTML={{__html: data[slug][keyName]}}/>
                      {
                        d.hasOwnProperty(keyName) ? <span ref={keyName}>{d[keyName]}</span> :
                          <span ref={keyName}></span>
                      }
                </span>
                )
                }

                })
              }
           </p>
            <p className={styleRetraite.signature}>Signature</p>
          </div>
         </page>
      </span>
    )
  }

  render() {
    if (this.state.hasOwnProperty('firstname') === false) return null;
    return (
      <Page title={this.state.firstname} >
        <button onClick={() => this.props.history.goBack()} className={classNames(button.button, button.stable, style.back)}><i className="ion-ios-arrow-thin-left"></i></button>
        {this.renderData()}
        {this.myFormsSuivit()}
        {this.myLetterButton()}
        {this.renderLetter()}
      </Page>
    )
  }

  /*render() {

    return (
        <Page title={this.state.user.name} subtitle={this.state.value.title}>
         <button onClick={() => this.props.history.goBack()} className={classNames(button.button, button.stable, style.back)}><i className="ion-ios-arrow-thin-left"></i></button>
          <div className={style.main}>
            <div>
              <div>
                <h3 className={styleRetraite.titleRetraite} >{this.state.value.title}</h3>
              </div>
              <span className={styleRetraite.dataTxt} dangerouslySetInnerHTML={{__html: this.state.value.infos }} />

            </div>
            <div>
              <h3 className={styleRetraite.titleRetraite} >Coordonnées</h3>


              <span>{ this.myFormsOrganism() }</span>
              <span>{ this.myFormsInterlocutor() }</span>
              <span>{ this.myFormsAddress() }</span>
              <span>{ this.myFormsInfos() }</span>
              <span>{ this.myFormsNumber() }</span>

              <span>{ this.myFormsSuivit() }</span>

              <span>{ this.myLetterButton() }</span>

            </div>
            <span>{ this.myLetter() }</span>
          </div>
        </Page>
    );
  }*/
}



