import React, {Component} from 'react'
import classNames from 'classnames'

import contactTypes from '../../constants/data/contactTypes.json'
import {createOptionsFromArray} from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/ContactRecallModal.scss'

export default class ContactModal extends Component {
    constructor(props, context) {
        super(props, context)

        const item = props.item || {}

        this.state = {
          import: true,
          lastName: item.civility || '',
            lastName: item.lastName || '',
            firstName: item.firstName || '',
            company: item.company || '',
            type: item.type || '',
            email: item.email || '',
            telephones: item.telephones || [{
              phone: '',
              type: 'fixe'
            }],
            address: item.address || {
                way: '',
                code: '',
                city: ''
              },
            birth: item.birth || {
              day: '',
              month: '',
              year: ''
            },
            commentary: item.commentary || '',
            statut: item.statut || '',
            edit: false,
            warn: false
        }

    }

    componentWillMount() {
        if (this.props.edit === true)
            this.state.edit = true
    }

    componentWillReceiveProps(newProps) {}

    onimport(value = false){
      this.setState({import: value})
    }

    save(e) {
        //create new object
        const {
            civility,
            lastName,
            firstName,
            company,
            type,
            email,
            statut,
            commentary
        } = this.state

        if(!lastName && !company) return this.setState({warn: true})

        const telephones = [...this.state.telephones]
        const address = {
            ...this.state.address
        }
        const birth = {
            ...this.state.birth
        }

        const newContact = {
            civility,
            lastName,
            firstName,
            company,
            type,
            email,
            commentary,
            telephones,
            address,
            statut,
            birth
        }

        this.props.onSave(newContact)
        this.close(e)
    }

    close(e) {
        e.stopPropagation()

        //close the portal
        this
            .props
            .closePortal()
    }

    edit(e) {
        e.stopPropagation()

        this.setState({edit: true})
    }

    remove(e) {
        this
            .props
            .onRemove()
        this.close(e)
    }

    addTelephone(e) {
        e.preventDefault()
        e.stopPropagation()
        this.setState({
            telephones: [
                ...this.state.telephones, {
                    phone: '',
                    type: 'fixe'
                }
            ]
        })
    }

    renderEdition() {
        return (
            <div className={style.edition} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                  <div>
                    <h1>{this.props.title}</h1>
                  </div>
                  <div>
                    <h2>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam odio orci, facilisis eget congue eget, fermentum eget massa. Nam id pretium ligula.</h2>
                  </div>
                </div>
              {this.state.import == false &&  <form className={style.content}>
                    <div className={style.row}>
                      <div className={style.group}>
                        <label>Civilité</label>
                          <select
                            type="text"
                            placeholder="Civilité"
                            value={this.state.civility}
                            onChange={(e) => this.setState({civility: e.target.value})} >
                            <option value={0}>Madame</option>
                            <option value={1}>Monsieur</option>
                            <option value={2}>Autre</option>
                          </select>
                      </div>

                        <div className={style.group}>
                            <label>Nom</label>
                            <input
                                type="text"
                                placeholder="Nom"
                                value={this.state.lastName}
                                style={ifStyle(this.state.warn && this.state.lastName === '', {border: '1px solid orangered'})}
                                onChange={e => this.setState({lastName: e.target.value})} />
                        </div>

                        <div className={style.group}>
                            <label>Prénom</label>
                            <input
                                type="text"
                                style={ifStyle(this.state.warn && this.state.firstName === '', {border: '1px solid orangered'})}
                                placeholder="Prénom"
                                value={this.state.firstName}
                                onChange={e => this.setState({firstName: e.target.value})} />
                        </div>
                    </div>

                    <div className={style.row}>
                      <div className={style.group}>
                        <label>Date de Naissance</label>
                        <input
                          type="number"
                          placeholder="Jour"
                          value={this.state.birth.day}
                          onChange={(e) => this.setState({
                            birth: {
                              ...this.state.birth,
                              day: e.target.value
                            }})} />

                      </div>

                      <div className={style.group}>
                        <label>&nbsp;</label>
                        <select
                          placeholder="Mois"
                          value={this.state.birth.month}
                          onChange={(e) => this.setState({
                            birth: {
                              ...this.state.birth,
                              month: e.target.value
                            }})} >
                          <option value="Janvier">Janvier</option>
                          <option value="Février">Février</option>
                          <option value="Mars">Mars</option>
                          <option value="Avril">Avril</option>
                          <option value="Mai">Mai</option>
                          <option value="Juin">Juin</option>
                          <option value="Juillet">Juillet</option>
                          <option value="Aout">Aout</option>
                          <option value="Septembre">Septembre</option>
                          <option value="Octobre">Octobre</option>
                          <option value="Novembre">Novembre</option>
                          <option value="Décembre">Décembre</option>
                        </select>
                      </div>


                      <div className={style.group}>
                        <label>&nbsp;</label>
                        <input
                          type="number"
                          placeholder="Année"
                          value={this.state.birth.year}
                          onChange={(e) => this.setState({
                            birth: {
                              ...this.state.birth,
                              year: e.target.value
                            }})} />
                      </div>

                    </div>

                    <div className={style.row}>
                      <div className={style.group}>
                      <label>Adresse</label>
                        <input
                            type="text"
                            placeholder="Adresse"
                            value={this.state.address.way}
                            onChange={e => this.setState({
                            address: {
                                ...this.state.address,
                                way: e.target.value
                            }
                        })} />
                      </div>
                    </div>
                <div className={style.row}>
                  <div className={style.group}>
                    <input
                        type="text"
                        placeholder="Code postal"
                        value={this.state.address.code}
                        onChange={e => this.setState({
                        address: {
                            ...this.state.address,
                            code: e.target.value
                        }
                    })} />
                  </div>
                  <div className={style.group}>
                    <input
                        type="text"
                        placeholder="Ville"
                        value={this.state.address.city}
                        onChange={e => this.setState({
                        address: {
                            ...this.state.address,
                            city: e.target.value
                        }
                    })} />
                  </div>
                  <div className={style.group}>
                    <input
                      type="text"
                      placeholder="Pays"
                      value={this.state.address.land}
                      onChange={e => this.setState({
                      address: {
                          ...this.state.address,
                          land: e.target.value
                      }
                    })} />
                  </div>
                </div>
                  <div className={style.row}>

                    <div className={style.group}>
                      <label>Email</label>
                      <input
                        type="email"
                        placeholder="Email"
                        value={this.state.email}
                        onChange={e => this.setState({email: e.target.value})} />
                    </div>
                  <div className={style.group}>
                    <label>Téléphone</label>
                    {this.state.telephones && this
                      .state
                      .telephones
                      .map((item, key) => (
                        <div key={key} className={style.inline}>
                          <input
                            type="text"
                            placeholder="Téléphone"
                            value={item.phone}
                            onChange={e => {
                              item.phone = e.target.value;
                              this.setState({});
                            }} />
                        </div>
                      ))}
                    </div>
                    <div className={style.group}>
                        <label>Statut du proche</label>
                        <select
                          placeholder="Proche à prévenir ou proche référent"
                          value={this.state.statut}
                          onChange={e => this.setState({statut: e.target.value})}
                          >
                          <option value="prev">Proche à prévenir</option>
                          <option value="ref">Proche référent</option>
                        </select>
                      </div>
                  </div>

                </form> }
              {this.state.import == true &&
              <div>
                <div className={classNames("col-md-6", style.importCard)} onClick={() => this.onimport()}>
                  <i className={"ion-ios-person"}></i> <br/>

                  Créer votre contact <br/>
                  <span>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam odio orci.
                </span>
                </div>
                <div className={classNames("col-md-6", style.importCard, style.disable)}>
                  <div className={style.overlay}>

                  </div>
                  <i className={classNames( "ion-ios-people")}></i> <br/>

                  Importer votre contact <br/>
                  <span>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam odio orci.
                </span>
                </div>
              </div>
              }
                <div className={style.footer}>
                  <div className={style.footerRight}>
                    <button
                      onClick={e => this.close(e)}
                      className={classNames(button.button, button.defaultLight, style.button)}
                    >
                      FERMER
                    </button>

                    {
                      this.state.import == false && <button
                        className={classNames(button.button, button.default, style.button)}
                        onClick={(e) => this.save(e)}>
                        VALIDER
                      </button>
                    }
                  </div>
                </div>
            </div>
        )
    }

    renderView() {
        return (
            <div className={style.main} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.state.civility} {this.state.firstName} {this.state.lastName && this
                            .state
                            .lastName
                            .toUpperCase()}</h1>
                </div>
                <div className={style.content}>
                  <div>
                    <h3>Anniversaire</h3>
                    <p>{this.state.birth.day} / {this.state.birth.month} / {this.state.birth.year}</p>
                  </div>
                    <div>
                        <h3>Téléphone</h3>
                        {this.state.telephones && this
                            .state
                            .telephones
                            .map((item, index) => (
                                <p key={index}>
                                    <strong>{item.type && item
                                            .type
                                            .toUpperCase() + ' :'}</strong>
                                    <a href={"tel:" + item.phone}>{item.phone}</a>
                                </p>
                            ))}
                    </div>

                    <div>
                        <h3>Email</h3>
                        <p>
                            <a href={"mailto:" + this.state.email}>{this.state.email}</a>
                        </p>
                    </div>

                    <div>
                        <h3>Adresse</h3>
                        <p>{this.state.address.way}</p>
                        <p>{this.state.address.code}</p>
                        <p>{this.state.address.city}</p>
                        <p>{this.state.address.land}</p>
                    </div>
                </div>
              <div className={style.footer}>
                <div className={style.footerRight}>
                  <button
                    className={classNames(button.button, button.default, style.button)}
                    onClick={(e) => this.edit(e)}>
                    <span className={button.text}>MODIFIER</span>
                  </button>

                  <button
                    onClick={e => this.remove(e)}
                    className={classNames(button.button, button.assertive, style.button)}>
                    <span className={button.text}>SUPPRIMER</span>
                  </button>

                  <button
                    onClick={e => this.close(e)}
                    className={classNames(button.button, button.default, style.button)}
                  >
                    ANNULER
                  </button>
                </div>
              </div>
            </div>
        )
    }

    render() {
        if (this.state.edit)
            return this.renderEdition()
        else
            return this.renderView()
    }

}

const ifStyle = (condition, style) => {
    if(condition) return style
    return {}
}



